<template>
  <div class="row">

    <div class="catalog-topzone col-sm-9">
      <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin/retailercarousel">Home</a>
      </li>
      <li class="breadcrumb-item active">Retailer Landing Page Carousel Builder</li>
    </ol>
      <table class="table">
        <thead>
          <tr>
            <th>Wishlist</th>
            <th>Start Date</th>
            <th>End Date</th>
            <th>Speed</th>
            <th>Status</th>
            <th>

            </th>
          </tr>
        </thead>
        <tbody>

          <tr v-for="carouselItem in carouselList" :key="carouselItem._id" className="carouselItem">
            <td>{{carouselItem.name}}</td>
            <td>{{carouselItem.start_date | formatDate}}</td>
            <td>{{carouselItem.end_date  | formatDate}}</td>
            <td>{{carouselItem.speed}}</td>
            <td>{{carouselItem.status}}</td>
            <td>
              <a class="btn btn-info" :href="OPT_API_ENDPOINT+'/optportal/services/redirection.html?redirectTo=optportal/showcase/OOO.html/wishlist/'+carouselItem._id" target="_tab">
                View
              </a>
              <a class="btn btn-warning" :href="'#/admin/retailercarousel/'+carouselItem._id">
                Edit
              </a>
              <button class="btn btn-danger" @click="deleteCarousel(carouselItem._id)">
                Delete
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="col-sm-3">
     <common-menu section='retailercarousel'></common-menu>
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi,OPT_API_ENDPOINT } from '../../../config';
export default {
  data: function() {
    return {
      carouselList: [],
      OPT_API_ENDPOINT:OPT_API_ENDPOINT || 'https://ddddd.com'
    }
  },
  methods: {
    fetchData: function() {
      var vm = this;
      axios.get(baseApi + '/wishLists?carousel=yes')
        .then(function(response) {
          console.log('rsponse',response)
          vm.carouselList = response.data;
        })
        .catch(function(error) {
          console.log('error', error)
        })
    },
    deleteCarousel: function(id){
      var vm = this;
      console.log('removing Id=====>',id);
      var flag = confirm('Are you sure, want to delete?');
      if(flag) {
        axios.delete(baseApi + '/wishLists/' + id)
          .then(function(response) {
            console.log('rsponse',response)
            // vm.carouselList = response.data;
            vm.fetchData();
          })
          .catch(function(error) {
            console.log('error', error)
          })
      }
    },
  },
  mounted: function() {
    console.log('mounted retailerCarouselList');

  },
  created: function() {
    console.log('created retailerCarouselList');
    this.fetchData();
  },
  updated: function() {
    console.log('updated retailerCarouselList');
  },
  components:{
    CommonMenu
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}
</style>



