
<template>
    <div class="row">

        <div class="catalog-topzone col-sm-9">

            <ol class="breadcrumb">
                <li class="breadcrumb-item">
                    <a href="#/admin">Home</a>
                </li>
                <li class="breadcrumb-item">
                    <a href="#/admin/callout">Callout</a>
                </li>

                <li class="breadcrumb-item active">Report</li>
            </ol>

            <form class="form-horizontal">
                <div class="form-group hide">
                     <label class="control-label col-sm-2" for="email">Report Type</label>
                    <div class="col-sm-10">
                        <select v-model="report.type" class="form-control">
                            <option value='brand'>Brand Report</option>
                            <option value='retailer'>Retailer Report</option>
                        </select>
                    </div>
                </div>
                <div class="form-group">

                    <label class="control-label col-sm-2" for="email">Showcase</label>
                    <div class="col-sm-10">
                        <select v-model="report.showcase" class="form-control">
                            <option disabled>Please Select showcae</option>
                            <option v-for="obj in showcases" v-bind:value="obj.slug" :key='obj._id'>
                                {{ obj.name }}
                            </option>
                        </select>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-2" for="email">Start Date </label>
                    <div class="col-sm-10">
                        <datepicker v-model="report.from" >></datepicker>
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-2" for="pwd">End Date</label>
                    <div class="col-sm-10">
                        <datepicker v-model="report.to" ></datepicker>
                    </div>
                </div>
                <div class="form-group">
                    <div class="col-sm-offset-2 col-sm-10">
                        <button class="btn btn-success" @click='saveCallout'>Save</button>
                        <a class="btn btn-secondary" href='#/admin/topzone'>Back</a>
                    </div>
                </div>
            </form>

        </div>
        <div class="col-sm-3">
            <common-menu section='rewardreports'></common-menu>
        </div>
    </div>
</template>
<script>
import Datepicker from 'vuejs-datepicker';
import axios from 'axios';
import _ from 'underscore';
import { baseApi } from '../../../config';
import CommonMenu from '../CommonMenu'
export default {
    data: function() {
        return {
            report: {
                type:'brand'
            },
            showcases: []
        }
    },
    props: ['id'],
    created: function() {
        this.getShowcases();

    },

    // props:['id'],
    methods: {
        goBack: function() {
            router.push('/admin')
        },
        serialize: function(obj) {
            var str = [];
            for (var p in obj)
                if (obj.hasOwnProperty(p)) {
                    str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
                }
            return str.join("&");
        },
        customFormatter(date) {
            return new Date(date);
        },
        getShowcases() {
            let vm = this;
            axios.get(baseApi + '/showcases')
                .then(function(response) {
                    vm.showcases = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        },
        saveCallout: function() {
            console.log('Calling Save User');
            var vm = this;
            if(this.report.from){
                let b = new Date(this.report.from);
                b = b.getFullYear()+'-'+(b.getMonth()+1)+'-'+b.getDate()
                this.report.from = b;
            }
            if(this.report.to){
                let b = new Date(this.report.to);
                b = b.getFullYear()+'-'+(b.getMonth()+1)+'-'+b.getDate();
                this.report.to = b;
            }
             window.open(baseApi + '/createReport?' + this.serialize(this.report),'_blank');
            // if(this.report.type == 'brand'){

            // //   window.location.href= baseApi + '/createBrandReport?' + this.serialize(this.report);
            // }else{
            //    window.open(baseApi + '/createRetailerReport?' + this.serialize(this.report),'_blank');
            // }
            console.log("window", baseApi + '/createReport?' + this.serialize(this.report))





        }
    },
    components: {
        Datepicker,
        CommonMenu
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}
</style>



