<template>
  <div class="catalog-topzone">
    <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item">
        <a href="#/admin/archivesmanagement">Archive</a>
      </li>
      <li class="breadcrumb-item active" v-if='category._id'>{{category.name}}</li>
      <li class="breadcrumb-item active" v-else>New</li>
    </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="checkForm" id='categoryunit'>
            <div class="form-group">
                <label class="control-label col-sm-2" >Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="category.name" name='name'>
                    <span v-show="errors.has('name')" class="red">{{ errors.first('name') }}</span>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" >Display Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="category.displayName" name='displayname'>
                    <span v-show="errors.has('displayname')" class="red">{{ errors.first('displayname') }}</span>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Slug</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="category.slug" name='slug'>
                    <span v-show="errors.has('slug')" class="red">{{ errors.first('slug') }}</span>
                </div>
            </div>
            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit' >Save</button>
                    <a  class="btn btn-default"  v-on:click="$router.push({path:`/admin/archivesmanagement/category`})">Cancel</a>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import axios from 'axios';
import _ from 'underscore';
import { baseApi } from '../../../config';
export default {
  data: function () {
    return {
      category: {}
    }
  },
  props: ['id'],
  created: function () { 
    this.fetchData();
  },
  methods: {
    fetchData: function() {
      console.log('id ', this.id);
      var vm = this;
      if(this.id){
        axios.get(baseApi + '/archiveCategories/' + this.id)
        .then(function(response) {
          console.log('callout', response.data)
          vm.category = response.data;
        }).catch(function(error) {
          console.log('error', error);
        });
      }
      
    },
    checkForm() {
      var vm = this;
      this.$validator.validateAll().then(res => {
        if (res) {
          vm.savecategory()
        } else {
          alert("Please correct all error!");
        }
      });
    },
    savecategory: function () {
      var vm = this;
      console.log('-==--=-      ');
      console.log(vm.category);
      if(this.category._id) {
        axios.put(baseApi + '/archiveCategories/' + vm.category._id, vm.category)
          .then(function (resp) {
            console.log('response  ', resp);
          })
          .catch(function (err) {
            console.log('Error  ', err);
          });
      } else {
        axios.post(baseApi + '/archiveCategories', vm.category)
          .then(function (resp) {
            console.log('response  ', resp);
            vm.$router.push({path:`/admin/archivesmanagement/category`})
          })
          .catch(function (err) {
            console.log('Error  ', err);
          });
      }
    }
  }
}
</script>

<style>
.red {
  color: red;
}
</style>



