<template>
  <div class="catalog-topzone">
    <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item">
        <a href="#/admin/archivesmanagement">Archive</a>
      </li>
      <li class="breadcrumb-item active" v-if='article._id'>{{article.title}}</li>
      <li class="breadcrumb-item active" v-else>New</li>
    </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="checkForm" id='articleunit'>
           
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Title</label>
                <div class="col-sm-10">
                    <input type="text" v-validate="{ required: true}"  class="form-control" v-model="article.title" name='title'>
                    <span v-show="errors.has('title')" class="red">{{ errors.first('title') }}</span>
                </div>
                
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">slug</label>
                <div class="col-sm-10">
                    <input type="text" v-validate="{ required: true}" class="form-control" v-model="article.slug" name='slug'>
                    <span v-show="errors.has('slug')" class="red">{{ errors.first('slug') }}</span>
                </div>
                
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Contributor</label>
                <div class="col-sm-10">
                    <select v-model="article.author" v-validate="{ required: true}" class="form-control" name='author'>
                        <option disabled>Please Select author</option>
                        <option v-for="obj in authors" v-bind:value="obj._id" :key='obj._id'>
                            {{ obj.name }}
                        </option>
                    </select>
                    <span v-show="errors.has('author')" class="red">Please select Contribtuor</span>
                </div>
            </div>

             <div class="form-group">
              <label class="control-label col-sm-2" for="email">Categories</label>
              <div class="col-sm-10">
                <selectize v-model="article.categories" v-validate="{ required: true}"  :settings='{}' multiple name='categories'>
                  <option v-for="obj in categoriesArr" :value="obj._id" :key='obj._id'>
                    {{ obj.name }}
                  </option>
                </selectize>
                <span v-show="errors.has('categories')" class="red">Please select categories</span>
              </div>
            </div>
            <div class="form-group hide">
              <label class="control-label col-sm-2" for="email">Categories</label>
              <div class="col-sm-10">
                <select v-model="article.categories" class="form-control" multiple>
                  <option disabled>Please Select Category</option>
                  <option v-for="obj in categoriesArr" v-bind:value="obj._id" :key='obj._id'>
                    {{ obj.name }}
                  </option>
                </select>
              </div>
            </div>
            <div class="form-group">
              <label class="control-label col-sm-2" for="pwd">Thumbnail</label>
              <div class="col-sm-6">
                <input v-if='article.thumbnail' type="file" class="form-control"  name='thumbnail_image'>
                <input v-else type="file" class="form-control" v-validate="{ required: true}"  name='thumbnail_image'>
                <span v-show="errors.has('thumbnail_image')" class="red">Please upload Thumbnail Image</span>
              </div>
              <div class="col-sm-3">

                <b-popover
                  v-if='article.thumbnail'
                  :target="'bioImage-' + article._id"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="article.thumbnail" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bioImage-' + article._id"
                  v-if='article.thumbnail'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!article.thumbnail'>N/A</span>

              </div>
            </div>

            <div class="form-group">
              <label class="control-label col-sm-2" for="pwd">Banner</label>
              <div class="col-sm-6">
                <input v-if='article.banner' type="file" class="form-control"  name='banner_image'>
                <input v-else type="file" class="form-control"   name='banner_image'>
                <span v-show="errors.has('banner_image')" class="red">Please upload Banner Image</span>
              </div>
              <div class="col-sm-3">

                <b-popover
                  v-if='article.banner'
                  :target="'bannerImage-' + article._id"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="article.banner" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bannerImage-' + article._id"
                  v-if='article.banner'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!article.banner'>N/A</span>

              </div>
            </div>

            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Article Content</label>
                <div id='test-editor' class="col-sm-10">
                    <vue-editor v-model="article.brief_content" :editorToolbar="customToolbar"></vue-editor>
                </div>
            </div>

            <div class="form-group hide">
                <label class="control-label col-sm-2" for="pwd">Body Content</label>
                <div id='test-editor' class="col-sm-10">
                    <vue-editor v-model="article.brief_content"></vue-editor>
                </div>
            </div>

            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Status</label>
                <div class="col-sm-10">
                    <select v-model="article.status" v-validate="{ required: true}" class="form-control">
                        <option disabled>Please Select visibility</option>
                        <option value='Active'>Active</option>
                        <option value='Draft'>Draft</option>
                        <option value='Deleted'>Deleted</option>
                        <option value='Archived'>Archived</option>
                    </select>
                </div>
            </div>

            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit'  >Save</button>
                     <a  class="btn btn-default"  v-on:click="$router.push({path:`/admin/archivesmanagement`})">Cancel</a>
                </div>
            </div>
        </form>
        <div v-if='id'>
           <legend>Article Products</legend>
        <table class="table">
          <thead>
            <tr>
              <th>Title</th>
              <th>URL</th>
              <th>Image</th>
              <th>
                <button v-bind:class="{'btn-success':true}" class="btn" @click="openBodyModal()">
                  Add
                </button>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="b in article.article_body" :key="b._id">
              <td>{{b.text}}</td>
               <td>{{b.url}}</td>
              <td>
                 <b-popover
                    v-if='b.image'
                    :target="'topZoneImage-' + b._id"
                    triggers="click blur">
                   <template slot="title">Preview</template>
                   <b-img :src="b.image" fluid alt="Responsive image" />
                  </b-popover>

                  <b-btn :id="'topZoneImage-' + b._id"
                  v-if='b.image'
                  variant="secondary">Preview</b-btn>
                  <span v-if='!b.image'>N/A</span>
               
              <td>
                <button class="btn btn-danger" v-on:click="deleteBody(b._id)">
                  Delete
                </button>
                <button class="btn btn-warning" v-on:click="openBodyModal(b._id)">
                  Edit
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        </div>
       
        <article-modal  v-if='show' @savearticleBodyUnit="savearticleBody_Unit" :articleBody="childArticleBody" :class='{in:show}' v-bind:style="{ display: display}"></article-modal>
    </div>
</template>

<script>
import axios from "axios";
import _ from "underscore";
import draggable from "vuedraggable";
import ArticleModal from "./ArticleModal";
import { baseApi } from "../../../config";
import { VueEditor } from "vue2-editor";
import Selectize from "vue2-selectize";
export default {
  data: function() {
    return {
      article: {
        categories: [],
        status: "Draft",
        article_body:[]
      },
      authors: [],
      categoriesArr: [],
      childArticleBody: null,
      display: "none",
      show: false,
      customToolbar: [
        ["bold", "italic", "underline", "strike"],
        ["blockquote", "code-block", "font"],

        [{ header: 1 }, { header: 2 }],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ script: "sub" }, { script: "super" }],
        [{ indent: "-1" }, { indent: "+1" }],
        [{ direction: "rtl" }],

        [{ size: ["small", false, "large", "huge"] }],
        [{ header: [1, 2, 3, 4, 5, 6, false] }],

        [{ color: [] }, { background: [] }],
        [{ align: [] }],

        ["link", "image", "code-block", "video"],

        ["clean"],
        // Extended toolbar buttons
        // [{ table: tableOptions }], // cursor needs to be in the table
        [{ table: "append-row" }], // cursor needs to be in the table
        [{ table: "remove-row" }], // cursor needs to be in the table
        [{ table: "append-col" }], // cursor needs to be in the table
        [{ table: "remove-col" }], // cursor needs to be in the table
        // [{ table: tableColors }], // cursor needs to be in the table
        [{ table: "remove-table" }] // cursor needs to be in the table
        // Extended toolbar buttons
      ]
    };
  },
  props: ["id"],
  created: function() {
    this.fetchData();
  },
  methods: {
    openBodyModal: function(id) {
      var vm = this;
      console.log("Opening Body modal");
      console.log(vm.article);
      var body = {};
      if (id) {
        body = _.findWhere(vm.article.article_body, { _id: id });
      }
      this.show = true;
      this.display = "inline";
      this.childArticleBody = body;
    },
    deleteBody: function(id) {
      let b = _.findWhere(this.article.body, { _id: id });
      this.article.body = _.without(this.article.body, b);
      this.savearticle();
    },
    savearticleBody_Unit: function(article_body) {
      var vm = this;
      if (article_body._id) {
        vm.article.article_body = _.map(vm.article.article_body, function(
          bodyObj
        ) {
          if (bodyObj._id == article_body._id) {
            return article_body;
          }
          return bodyObj;
        });
      } else {
        vm.article.article_body.push(article_body);
      }
      this.savearticle();
    },
    checkForm() {
      var vm = this;
      this.$validator.validateAll().then(res => {
        if (res) {
          vm.doThis()
        } else {
          alert("Please correct all error!");
        }
      });
    },
    doThis: function() {
      // if (!this.checkForm()) {
      //   return;
      // }
      var vm = this;
      if (
        document.querySelector("[name=thumbnail_image]").value ||
        document.querySelector("[name=banner_image]").value
      ) {
        const formData = new FormData(document.querySelector("#articleunit"));
        let url = baseApi + "/commons/upload";
        axios
          .post(url, formData)
          // get data
          .then(x => {
            var data = x.data;
            console.log("data", data);
            var thumbnail_image = _.findWhere(data, { key: "thumbnail_image" });
            var banner_image = _.findWhere(data, { key: "banner_image" });

            if (thumbnail_image) vm.article.thumbnail = thumbnail_image.value;
            if (banner_image) vm.article.banner = banner_image.value;
            vm.savearticle();
          })
          .catch(e => {
            console.log("Something went wring", e);
          });
        return false;
      } else {
        this.savearticle();
      }
    },
    getAuthors: function() {
      var vm = this;
      axios
        .get(baseApi + "/archiveAuthors")
        .then(function(response) {
          console.log(
            "-=-=-=-=-=-=-=-=-=-=autherewewerws-=-=-=-=-=-=-------=-=-=-=-="
          );
          console.log("response", response);
          vm.authors = response.data;
        })
        .catch(function(error) {
          console.log("error", error);
        });
    },
    fetchData: function() {
      console.log("id ", this.id);
      var vm = this;
      vm.getAuthors();
      if (this.id) {
        axios
          .get(baseApi + "/archiveArticles/" + this.id + "?source=admin")
          .then(function(response) {
            vm.article = response.data;
          })
          .catch(function(error) {
            console.log("error", error);
          });
      }
      axios
        .get(baseApi + "/archiveCategories")
        .then(function(response) {
          console.log("response", response);
          vm.categoriesArr = response.data;
        })
        .catch(function(error) {
          console.log("error", error);
        });
    },
    savearticle: function() {
      var vm = this;
      console.log("-==--=-      ");
      console.log(vm.article);
      if (this.article._id) {
        axios
          .put(baseApi + "/archiveArticles/" + vm.article._id, vm.article)
          .then(function(resp) {
            console.log("response  ", resp);
          })
          .catch(function(err) {
            console.log("Error  ", err);
          });
      } else {
        axios
          .post(baseApi + "/archiveArticles", vm.article)
          .then(function(resp) {
            console.log("response  ", resp);
            vm.$router.push({
              path: `/admin/articlecomponent/${resp.data._id}`
            });
            window.location.reload();
            
          })
          .catch(function(err) {
            console.log("Error  ", err);
          });
      }
    }
  },
  components: {
    VueEditor,
    draggable,
    ArticleModal,
    Selectize
  }
};
</script>
<style >
@import "~selectize/dist/css/selectize.bootstrap3.css";
.red {
  color: red;
}
.ql-toolbar.ql-snow+.ql-container.ql-snow {
    height: 30rem;
}
</style>


