<template>
  <div class="catalog-topzone col-sm-12">
    <paginate :page-count="page_count" :click-handler="clickHandlerCallback" class="pagination" :container-class="'className'">
        <span slot="prevContent"> Previous </span>
        <span slot="nextContent"> Next </span>
      </paginate>
      <div style="text-align:right"><a href="#/admin/articlecomponent/new" v-bind:class="{'btn-success':true}" class="btn"> Add </a></div>
     <el-table
    :data="articles"
    style="width: 100%">
    
    <el-table-column
      label="Publish Date"
      width="180">
      <template slot-scope="scope">
        <i class="el-icon-time"></i>
        <span style="margin-left: 10px">{{ scope.row.created | humenDate}}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="Title"
      width="180" 
      property='title'>
    </el-table-column>
    
    <el-table-column
      label="Contributor"
      width="180" 
      property='author.name'>
    </el-table-column>

    <el-table-column
      label="Status"
      width="180" 
      >
      <template slot-scope="scope">
        <el-tag :type="(scope.row.status == 'Deleted') ? 'danger':''">{{scope.row.status}}</el-tag>
      </template>
    </el-table-column>
    <el-table-column
      label="Thumbnail Image"
      width="120" >
       <template slot-scope="scope">
           <b-popover
                  v-if='scope.row.thumbnail'
                  :target="'bannerImage-' + scope.row.thumbnail"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="scope.row.thumbnail" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bannerImage-' + scope.row.thumbnail"
                  v-if='scope.row.thumbnail'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!scope.row.thumbnail'>N/A</span>
       </template>
    </el-table-column>
    <el-table-column
      label="Banner Image"
      width="120" >
       <template slot-scope="scope">
           <b-popover
                  v-if='scope.row.banner'
                  :target="'bannerImage-' + scope.row.banner"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="scope.row.banner" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bannerImage-' + scope.row.banner"
                  v-if='scope.row.banner'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!scope.row.banner'>N/A</span>
       </template>
    </el-table-column>




    <el-table-column
      label="Operations" fixed="right" width='180'>
      <template slot-scope="scope">
       <a type='button' :href="'#/admin/articlecomponent/'+ scope.row._id"><el-button
          size="mini"
          type="warning"
          >Edit</el-button>  </a>
        <el-button
          size="mini"
          type="danger"
          class="delete-btn"
          v-if='scope.row.status!="Deleted"'
          @click="deleteCallout(scope.row._id)">Delete</el-button>
      </template>
    </el-table-column>
  </el-table>
    <table class="table hide">
      <thead>
        <tr>
          <th>Title</th>
          <th>Thumbnail Image</th>
          <th>Banner Image</th>
          <th>Status</th>
          <th> <a href="#/admin/articlecomponent/new" v-bind:class="{'btn-success':true}" class="btn"> Add </a> </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="article in articles" :key="article._id">
          <td>{{article.title}}</td>
          <td>
             <b-popover
                  v-if='article.thumbnail'
                  :target="'bannerImage-' + article.thumbnail"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="article.thumbnail" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bannerImage-' + article.thumbnail"
                  v-if='article.thumbnail'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!article.thumbnail'>N/A</span>

          </td>
          <td>
             <b-popover
                  v-if='article.banner'
                  :target="'bannerImage-' + article.banner"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="article.banner" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bannerImage-' + article.banner"
                  v-if='article.banner'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!article.banner'>N/A</span>

          </td>
          <td>{{article.status}}
          <td>
            <button class="btn btn-danger hide" v-on:click="deleteCallout(article._id)"> Delete </button>
            <a class="btn btn-warning" :href="'#/admin/articlecomponent/'+ article._id"> Edit </a>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
import axios from "axios";
import { baseApi } from "../../../config";

export default {
  data: function() {
    return {
      articles: [],
      page_count: 0,
      current_page: 1
    };
  },
  methods: {
    deleteCallout(id){
      
      let vm = this;
     let url =  baseApi + "/archiveArticles/"+id
     
      axios.put(url,{status:'Deleted'}).then(function(){
        vm.fetchData()
      }).error(function(){
        console.log("error", error);
      })
    },
    clickHandlerCallback: function(pageNum) {
      console.log("Hello -= =- =- -= " + pageNum);

      this.current_page = pageNum;
      this.fetchData();
    },
    fetchData: function() {
      var vm = this;
      axios
        .get(baseApi + "/archiveArticles?page=" + this.current_page)
        .then(function(response) {
          console.log("-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-------=-=-=-=-=");
          console.log("response", response);
          var totCount = vm.current_page
          console.log('@totCount ======>', totCount)
          if(response.data.next){
            totCount = vm.current_page
            totCount = Math.ceil(
            response.data.totalCount / (response.data.currentCount/(response.data.next-1))
          );
          }
           
          vm.page_count = totCount;
          console.log('@vm.page_count=====>', vm.page_count)
          vm.articles = response.data.data;
        })
        .catch(function(error) {
          console.log("error", error);
        });
    }
  },
  mounted: function() {
    console.log("mounted 654 654 654 6546546 ");
  },
  created: function() {
    console.log("created 66565656565  5 65 654745 754 ");
    this.fetchData();
  }
};
</script>

<style>

</style>



