<template>
    <div id="menumodal" class="modal fade" role="dialog">
        <div class="modal-dialog">

            <!-- Modal content-->
            <div class="modal-content" style="position:absolute">
                <div class="modal-header">

                    <h4>{{$parent.$parent.menu.label}}/{{$parent.menuunit.label}}/{{listing.anchorText}} Listings</h4>
                    <button type="button" class="close pull-right" data-dismiss="modal" @click="$emit('close')">&times;</button>
                </div>
                <div class="modal-body">

                    <table class="table">
                        <thead>
                            <tr>
                                <th>anchorLink</th>
                                <th>Text</th>
                                <th></th>
                                <th>
                                    <button v-bind:class="{'btn-success':true}" class="btn" @click="addnewrow">
                                        Add
                                    </button>
                                </th>
                            </tr>
                        </thead>
                        <draggable 
                        v-model="listing.listings" 
                        :options="{group:'people'}" 
                        element="tbody"
                        @start="drag=true" 
                        @end="savelistings">
                            <tr v-for="listobj in listing.listings" :key="listobj._id">
                                <td><input type='text' class="form-control" v-model="listobj.anchorLink" /></td>
                                <td><input type='text' class="form-control" v-model="listobj.anchorText" /></td>
                                <td>
                                    <button class="btn btn-danger" v-on:click="deleteListing(listobj._id)">
                                        Delete
                                    </button>
                                    <button class="btn btn-secondary" @click="updateListing(listobj._id)">
                                        save
                                    </button>
                                </td>
                            </tr>
                        </draggable>
                    </table>

                </div>

            </div>
        </div>

    </div>
</template>

<script>
import draggable from 'vuedraggable'
import axios from 'axios';
import { baseApi } from '../../../config';
import _ from 'underscore';
export default {
    props: ['listing'],
    data() {
        return {
        };
    },
    created() {

    },
    methods: {

        updateListing(listid) {
            this.$emit('savelistinglistings', this.listing);

        },
        addnewrow() {
            this.listing.listings.unshift({})
        },
        deleteListing(listid) {
            var isDelete = window.confirm('are you sure, you want to delete?');
            if (isDelete) {
                this.listing.listings = _.filter(this.listing.listings, function(list) {
                    if (list._id != listid) return true;
                    return false;
                })
                this.$emit('savelistinglistings', this.listing);
            }


        },
        savelistings() {
            this.$emit('savelistinglistings', this.listing);
        }
    },
    updated() {
        console.log('callout11', this.headermenu)
    },
    components: {
        draggable

    }
}
</script>


