<template>
    <div class="catalog-topzone">
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item">
                <a href="#/admin/category">Category</a>
            </li>
            <li class="breadcrumb-item active" v-if='category._id'>{{category.name}}</li>
            <li class="breadcrumb-item active" v-else>New</li>
        </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="doThis" id='categoryunit'>

            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="category.name">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Slug</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="category.slug">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Visibility</label>
                <div class="col-sm-10">
                    <select v-model="category.visibility" class="form-control" multiple>
                        <option disabled>Please Select visibility</option>
                        <option value='LEFT'>LEFT</option>
                        <option value='TOP'>TOP</option>
                    </select>
                </div>
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="email">Showcases</label>
                <div class="col-sm-10">
                    <select v-model="category.showcases" class="form-control" multiple>
                        <option disabled>Please Select showcae</option>
                        <option v-for="obj in showcases" v-bind:value="obj.slug" :key='obj._id'>
                            {{ obj.name }}
                        </option>
                    </select>
                </div>
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="email">Children Categories</label>
                <div class="col-sm-10">
                    <select v-model="category.children" class="form-control" multiple>
                        <option disabled>Please Select Category</option>
                        <option v-for="obj in categories"  v-if="category._id != obj._id" v-bind:value="obj._id" :key='obj._id'>
                            {{ obj.name }}
                        </option>
                    </select>
                </div>
            </div>
            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit' >Save</button>
                    <a class="btn btn-secondary" href='#/admin/category'>Back</a>
                </div>
            </div>
        </form>
    </div>
</template>

<script>

import axios from 'axios';
import _ from 'underscore';
import { baseApi } from '../../../config';
export default {
    data: function() {
        return {
            category: {
                children:[]
            },
            show: false,
            showcases: [],
            callouts: [],
            childcallout: null,
            display: 'none',
            categories:[]
        }
    },
    props: ['id'],
    created: function() {
        console.log('this', this.id)
        var vm = this;
        this.getCategories();
        console.log('user instance created', this.$route.params)
        if (this.id) {
            axios.get(baseApi + '/categories/' + this.id)
                .then(function(response) {
                    console.log('callout', response.data)
                    vm.category = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        }
         this.getShowcases();
          this.getCategories();

    },
    // props:['id'],
    methods: {
        goBack: function() {
            router.push('/admin')
        },
        getShowcases() {
            let vm = this;
            axios.get(baseApi + '/showcases')
                .then(function(response) {
                    vm.showcases = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        },
         getCategories() {
            let vm = this;
            axios.get(baseApi + '/categories')
                .then(function(response) {
                    vm.categories = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        },
       doThis: function() {
            console.log('do this')
            //here do what u want
            this.saveBrand()
            var vm = this;

        },
        saveBrand: function() {
            console.log('Calling Save User');
            var vm = this;
            if (vm.category._id) {
                axios.put(baseApi + '/categories/' + vm.category._id, vm.category)
                    .then(function(response) {
                        console.log(response);

                        vm.show = false;
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else {
                axios.post(baseApi + '/categories', vm.category)
                    .then(function(response) {
                        console.log(response);

                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            }

        },
    },
    components: {
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

</style>



