<template>
  <div id="myModal" class="modal fade" role="dialog">
    <div class="modal-dialog">
      <!-- Modal content-->
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title" v-if='articleBody._id'>Edit</h4>
          <h4 class="modal-title" v-else>Add</h4>
          <button type="button" class="close" data-dismiss="modal" v-on:click="$parent.show=false">&times;</button>
          
        </div>
        <div class="modal-body">
          <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="doThis" id='formunit'>

            <div class="form-group">
              <label class="control-label col-sm-3" for="email">Text</label>
              <div class="col-sm-9">
                <input type="text" class="form-control" v-model="articleBody.text">
              </div>
            </div>

            <div class="form-group">
              <label class="control-label col-sm-3" for="email">Description</label>
              <div class="col-sm-9">
                <input type="text" class="form-control" v-model="articleBody.description">
              </div>
            </div>
            
            <div class="form-group">
              <label class="control-label col-sm-3" for="pwd">Image</label>
              <div class="col-sm-9">
                <input type="file" class="form-control" name='body_image'>
              </div>
            </div>
             <div class="form-group hide">
              <label class="control-label col-sm-3" for="email">Image Url</label>
              <div class="col-sm-9">
                <input type="text" class="form-control" v-model="articleBody.image">
              </div>
            </div>
            <div class="form-group ">
              <label class="control-label col-sm-3" for="email">Action Url</label>
              <div class="col-sm-9">
                <input type="text" class="form-control" v-model="articleBody.url">
              </div>
            </div>

            <div class="form-group">
              <div class="col-sm-offset-3 col-sm-9">
                <button type='submit' class="btn btn-secondary">Save</button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from 'underscore';
import axios from 'axios';
import { baseApi } from '../../../config';

export default {
  props: ['articleBody'],
  data() {
    return {
      show: false
    };
  },
  created() {
  },
  methods: {
    savearticleBody: function() {
      this.$emit('savearticleBodyUnit', this.articleBody);
    },
    doThis: function() {
      console.log('do this')
      //here do what u want
      var vm = this;
      if (document.querySelector('[name=body_image]').value) {
        const formData = new FormData(document.querySelector('#formunit'));
        let url = baseApi + '/commons/upload';
        axios.post(url, formData)
          // get data
          .then(x => {
            var data = x.data;
            console.log('data',data)
            var body_image = _.findWhere(data,{key:'body_image'});
            if(body_image)  vm.articleBody.image = body_image.value;
            vm.savearticleBody();
          }).catch(e => {
            console.log('Something went wring', e)
          })
          return false;
        }else{
          this.savearticleBody();
        }
      }
    },
    updated() {
      console.log('callout11', this.callout)
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .modal-content {
    position: absolute;
  }
</style>



