<template>
    <div class="catalog-topzone">
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item">
                <a href="#/admin/brand">Brand</a>
            </li>
            <li class="breadcrumb-item active" v-if='brand._id'>{{brand._id}}</li>
            <li class="breadcrumb-item active" v-else>New</li>
        </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="doThis" id='brandunit'>

            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="brand.name">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Display Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="brand.displayName">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Slug</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="brand.slug">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Visibility</label>
                <div class="col-sm-10">
                    <select v-model="brand.visibility" class="form-control" multiple>
                        <option disabled>Please Select visibility</option>
                        <option value='LEFT'>LEFT</option>
                        <option value='TOP'>TOP</option>
                    </select>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd"> Catalog Topzone Image</label>
                <div class="col-sm-6">
                    <input type="file" class="form-control" name='topzone_image'>
                </div>
                <div class="col-sm-3 hey-lala">

                  <b-popover
                    v-if='brand.top_zone_image'
                    :target="'topZoneImage-' + brand.slug"
                    triggers="click blur">
                   <template slot="title">Preview</template>
                   <b-img :src="brand.top_zone_image" fluid alt="Responsive image" />
                  </b-popover>

                  <b-btn :id="'topZoneImage-' + brand.slug"
                  v-if='brand.top_zone_image'
                  variant="secondary">Preview</b-btn>
                  <span v-if='!brand.top_zone_image'>N/A</span>

                </div>
            </div>
            <div class="form-group hide">
                <label class="control-label col-sm-2" for="pwd">Designer Name</label>
                <div class="col-sm-9">
                    <input type="text" class="form-control" v-model='brand.bio.name'>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Designer Bio Image</label>
                <div class="col-sm-6">
                    <input type="file" class="form-control" name='bio_image'>
                </div>
                <div class="col-sm-3">

                  <b-popover
                    v-if='brand.bio.image'
                    :target="'bioImage-' + brand.slug"
                    triggers="click blur">
                   <template slot="title">Preview</template>
                   <b-img :src="brand.bio.image" fluid alt="Responsive image" />
                  </b-popover>

                  <b-btn :id="'bioImage-' + brand.slug"
                    v-if='brand.bio.image'
                    variant="secondary"> Preview</b-btn>
                  <span v-if='!brand.bio.image'>N/A</span>

                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Designer Bio Description</label>

                <div id='test-editor' class="col-sm-10">
                    <vue-editor v-model="brand.bio.description"></vue-editor>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Showcases</label>
                <div class="col-sm-10">
                    <select v-model="brand.showcases" class="form-control" multiple>
                        <option disabled>Please Select showcae</option>
                        <option v-for="obj in showcases" v-bind:value="obj.slug" :key='obj._id'>
                            {{ obj.name }}
                        </option>
                    </select>
                </div>
            </div>
            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit'>Save</button>
                    <a class="btn btn-secondary" href='#/admin/brand'>Back</a>
                </div>
            </div>
        </form>
        <legend>Settings</legend>
        <keyvaluecrud @save='updateSettings' :settings='brand.settings'></keyvaluecrud>
    </div>
</template>

<script>

import axios from 'axios';
import _ from 'underscore';
import Vue from 'vue';
import { baseApi } from '../../../config';
import CrudKeyValueModal from '../../common/keyvaluecrud';
import { VueEditor } from 'vue2-editor'

export default {
    data: function() {
        return {
            brand: {},
            show: false,
            showcases: [],
            callouts: [],
            childcallout: null,
            display: 'none'
        }
    },
    props: ['id'],
    created: function() {
        console.log('this', this.id)
        var vm = this;
        console.log('user instance created', this.$route.params)
        if (this.id) {
            axios.get(baseApi + '/brands/' + this.id)
                .then(function(response) {
                    console.log('callout', response.data)
                    var data = response.data;
                    data.bio = data.bio || {};
                    data.settings = data.settings || {};
                    vm.brand = data
                }).catch(function(error) {
                    console.log('error', error)

                })
        }
        this.getShowcases();

    },
    // props:['id'],
    methods: {
        goBack: function() {
            router.push('/admin')
        },
        updateSettings(settings){
            console.log('Settings',settings)
            this.brand.settings = settings;
            this.saveBrand()
        },
        getShowcases() {
            let vm = this;
            axios.get(baseApi + '/showcases')
                .then(function(response) {
                    vm.showcases = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        },
        doThis: function() {
            console.log('do this')
            //here do what u want
            var vm = this;
            if (document.querySelector('[name=topzone_image]').value || document.querySelector('[name=bio_image]').value) {
                const formData = new FormData(document.querySelector('#brandunit'));
                let url = baseApi + '/commons/upload';
                axios.post(url, formData)
                    // get data
                    .then(x => {
                        var data = x.data;
                        console.log('data', data)
                        var topzone = _.findWhere(data, { key: 'topzone_image' });
                        var bio = _.findWhere(data, { key: 'bio_image' });
                        if (topzone) vm.brand.top_zone_image = topzone.value;
                        if (bio) {
                            vm.brand.bio = vm.brand.bio || {};
                            vm.brand.bio.image = bio.value;
                        }
                        vm.saveBrand();

                    }).catch(e => {
                        console.log('Something went wring', e)
                    })
                return false;
            } else {
                this.saveBrand();
            }
        },
        saveBrand: function() {
            console.log('Calling Save User');
            var vm = this;
            if (vm.brand._id) {
                axios.put(baseApi + '/brands/' + vm.brand._id, vm.brand)
                    .then(function(response) {
                        console.log(response);

                        vm.show = false;
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else {
                axios.post(baseApi + '/brands', vm.brand)
                    .then(function(response) {
                        console.log(response);

                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            }

        },
    },
    components: {
       Keyvaluecrud: CrudKeyValueModal,
       VueEditor
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}
</style>



