<template>
  <div class="catalog-topzone">
    <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item">
        <a href="#/admin/callout">Callout</a>
      </li>
      <li class="breadcrumb-item active" v-if='callout._id'>{{callout._id}}</li>
      <li class="breadcrumb-item active" v-else>New</li>
    </ol>
    <form class="form-horizontal">
      <div class="form-group">
        <label class="control-label col-sm-2" for="email">Showcase</label>
        <div class="col-sm-10">
          <select v-model="callout.showcase" class="form-control" @change="getRetailers">
            <option disabled>Please Select showcae</option>
            <option v-for="obj in showcases" v-bind:value="obj._id" :key='obj._id'>
              {{ obj.name }}
            </option>
          </select>
        </div>
      </div>
      <div class="form-group">
        <label class="control-label col-sm-2" for="email">Retailer</label>
        <div class="col-sm-10">
          <select v-model="callout.retailerId" class="form-control">
            <option v-for="obj in retailers" v-bind:value="obj.company_id" :key='obj.company_id'>
              {{ obj.name }}
            </option>
          </select>
        </div>
      </div>
      <div class="form-group">
        <label class="control-label col-sm-2" for="pwd">Page</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="callout.page">
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
          <button class="btn btn-success" @click='saveCallout'>Save</button>
          <a class="btn btn-secondary" @click='goBack'>Back</a>
        </div>
      </div>
    </form>
    <legend>Page Callouts</legend>
    <table class="table table-hover">
      <thead>
        <tr>
          <th>Anchor Link</th>
          <th>Desktop Image</th>
          <th>Mobile Image</th>
          <th>Image Column</th>
          <th>Column Offset</th>
          <th>
            <button v-bind:class="{'btn-success':true}" class="btn" @click="openCalloutModal()">
              Add
            </button>
          </th>
        </tr>
      </thead>
      <draggable
        v-model="callout.callouts"
        :options="{group:'people'}"
        @start="drag=true"
        element="tbody"
        @end="saveCallout">
        <tr v-for="callout in callout.callouts" :key="callout._id">
            <td>{{callout.anchorLink}}</td>
            <td>

              <b-popover
                v-if='callout.desktop_image'
                :target="'deskImage-' + callout._id"
                triggers="click blur">
               <template slot="title">Desktop Image</template>
               <b-img :src="callout.desktop_image" fluid alt="Responsive image" />
              </b-popover>

              <b-btn :id="'deskImage-' + callout._id"
                v-if='callout.desktop_image'
                variant="secondary"> Preview</b-btn>
              <span v-if='!callout.desktop_image'>N/A</span>

            </td>
            <td>

              <b-popover
                v-if='callout.mobile_image'
                :target="'mobileImage-' + callout._id"
                triggers="click blur">
               <template slot="title">Mobile Image</template>
               <b-img :src="callout.mobile_image" fluid alt="Responsive image" />
              </b-popover>

              <b-btn :id="'mobileImage-' + callout._id"
                v-if='callout.mobile_image'
                variant="secondary"> Preview</b-btn>
              <span v-if='!callout.mobile_image'>N/A</span>

            </td>
            <td>{{callout.imagecolumn}}</td>
            <td>{{callout.imagecolumnoffset}}</td>
            <td>
              <button class="btn btn-danger" v-on:click="deleteCallout(callout._id)">
                Delete
              </button>
              <button class="btn btn-warning" v-on:click="openCalloutModal(callout._id)">
                Edit
              </button>
            </td>
          </tr>
      </draggable>
    </table>
    <callout-modal v-if='show' @savecalloutunit="savecallout_unit" :callout="childcallout" :class='{in:show}' v-bind:style="{ display: display}"></callout-modal>
  </div>
</template>

<script>

import axios from 'axios';
import draggable from 'vuedraggable'
import CalloutModal from './CalloutModal';
import _ from 'underscore';
import { baseApi } from '../../../config';
export default {
  data: function() {
    return {
      callout: {},
      show: false,
      showcases: [],
      callouts: [],
      childcallout: null,
      display: 'none',
      retailers: []
    }
  },
  props: ['id'],
  created: function() {
    console.log('this', this.id)
    var vm = this;
    console.log('user instance created', this.$route.params)
    if (this.id) {
      axios.get(baseApi + '/callouts/' + this.id)
        .then(function(response) {
          console.log('callout', response.data)
          vm.callout = response.data;
        }).catch(function(error) {
          console.log('error', error)

        })
    }
    this.getShowcases();

  },

  // props:['id'],
  methods: {
    goBack: function() {
      history.back()
    },
    getRetailers() {
      var vm = this;
      let showcaseId = this.callout.showcase;
      console.log('showcases', showcaseId)
      let showcase = _.findWhere(this.showcases, { _id: showcaseId });
      axios.get(baseApi + '/retailers?optId=' + showcase.optId)
        .then(function(response) {
          console.log('callout', response.data)
          vm.retailers = response.data;
        }).catch(function(error) {
          console.log('error', error)

        })
    },
    savecallout_unit(calloutunit) {
      var vm = this;
      console.log('calloutunit', calloutunit);
      if (calloutunit._id) {
        vm.callout.callouts = _.map(vm.callout.callouts, function(calloutobj) {
          if (calloutobj._id == calloutunit._id) {
            return calloutunit;
          }
          return calloutobj;
        })
      } else {
        vm.callout.callouts.push(calloutunit);
      }

      this.saveCallout()
    },
    getShowcases() {
      let vm = this;
      axios.get(baseApi + '/showcases')
        .then(function(response) {
          vm.showcases = response.data;
        }).catch(function(error) {
          console.log('error', error)

        })
    },
    saveCallout: function() {
      console.log('Calling Save User');
      var vm = this;
      if (vm.callout._id) {
        axios.put(baseApi + '/callouts/' + vm.callout._id, vm.callout)
          .then(function(response) {
            console.log(response);

            vm.show = false;
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        axios.post(baseApi + '/callouts', vm.callout)
          .then(function(response) {
            console.log(response);
            router.push('/admin')
          })
          .catch(function(error) {
            console.log(error);
          });
      }

    },
    openCalloutModal(id) {
      console.log('Callingddddd')
      var callout = {};
      if (id) {
        callout = _.findWhere(this.callout.callouts, { _id: id })
      }
      this.show = true;
      this.display = 'inline'
      this.childcallout = callout
      console.log('callout', callout);

      console.log('this ', this.show)

    },
    deleteCallout(id) {
      let callout = _.findWhere(this.callout.callouts, { _id: id })
      this.callout.callouts = _.without(this.callout.callouts, callout);
      this.saveCallout()
    }
  },
  components: {
    CalloutModal,
    draggable,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

</style>



