import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/home/Index';
import Catalog from '@/components/catalog/Index';
import Product from '@/components/product/Index';
import Menu from '@/components/admin/menu/Menu';
import MenuUnit from '@/components/admin/menu/MenuUnit';
import Callout from '@/components/admin/callout/Callout';
import CalloutUnit from '@/components/admin/callout/CalloutUnit';
import Topzone from '@/components/admin/topzone/Topzone';
import TopzoneUnit from '@/components/admin/topzone/TopzoneUnit';
import Brand from '@/components/admin/brand/Brand';
import BrandUnit  from '@/components/admin/brand/BrandUnit';
import Category from '@/components/admin/category/Category';
import CategoryUnit  from '@/components/admin/category/CategoryUnit';
import Showcase from '@/components/admin/showcase/Showcase';
import ShowcaseUnit  from '@/components/admin/showcase/ShowcaseUnit';
import Reward from '@/components/admin/reward/Reward';
import RewardUnit  from '@/components/admin/reward/RewardUnit';
import Report from '@/components/admin/report/Report';
import ProductAdmin from '@/components/admin/product/Product';
import ProductAdminUnit from '@/components/admin/product/ProductUnit';
import ProductAttributes from '@/components/admin/productattributes/ProductAttribute';
import ProductAttributesUnit from '@/components/admin/productattributes/ProductAttributeUnit';
import RetailerCarousel from '@/components/admin/retailercarousel/RetailerCarousel';
import RetailerCarouselUnit from '@/components/admin/retailercarousel/RetailerCarouselUnit';
import ArchivesManagement from '@/components/admin/archives/archives'
import AuthorUnit from '@/components/admin/archives/AuthorUnit'
import CategoryComponentUnit from '@/components/admin/archives/CategoryUnit'
import ArticleComponentUnit from '@/components/admin/archives/ArticleUnit'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Callout,
    },
    {
      path: '/catalog',
      name: 'Catalog',
      component: Catalog,
    },
    {
      path: '/product/:id',
      name: 'Product  ',
      component: Product,
    },
    {
      path: '/admin/menulist',
      name: 'Menu',
      component: Menu,
    },
    {
      path: '/admin/menulist/new',
      name: 'MenulistUnit',
      component: MenuUnit,
    },
    {
      path: '/admin/menulist/:id',
      name: 'UpdateMenulistUnit',
      component: MenuUnit,
      props: true
    },
    {
      path: '/admin/retailercarousel',
      name: 'CarouselList',
      component: RetailerCarousel     
    },
    {
      path: '/admin/retailercarousel/new',
      name: 'CarouselListUnit',
      component: RetailerCarouselUnit
    },
    {
      path: '/admin/retailercarousel/:id',
      name: 'CarouselListUnit',
      component: RetailerCarouselUnit,
      props:true
    },
   
    {
      path: '/admin/topzone',
      name: 'Topzone',
      component: Topzone,
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Callout,
    },
    {
      path: '/admin/callout',
      name: 'AdminCallout',
      component: Callout,
    },
    {
      path: '/admin/callout/new',
      name: 'CalloutUnit',
      component: CalloutUnit,
    },
    {
      path: '/admin/callout/:id',
      name: 'UpdateCalloutUnit',
      component: CalloutUnit,
      props: true
    },
    {
      path: '/admin/topzone/new',
      name: 'TopzoneUnit',
      component: TopzoneUnit,
    },
    {
      path: '/admin/topzone/:id',
      name: 'UpdateTopzoneUnit',
      component: TopzoneUnit,
      props: true
    },
     {
      path: '/admin/brand',
      name: 'Brand',
      component: Brand,
    },
    {
      path: '/admin/brand/new',
      name: 'BrandUnit',
      component: BrandUnit,
    },
    {
      path: '/admin/brand/:id',
      name: 'UpdateBrandUnit',
      component: BrandUnit,
      props: true
    },
    {
      path: '/admin/category',
      name: 'Category',
      component: Category,
    },
    {
      path: '/admin/category/new',
      name: 'CategoryUnit',
      component: CategoryUnit,
    },
    {
      path: '/admin/category/:id',
      name: 'UpdateCategoryUnit',
      component: CategoryUnit,
      props: true
    },
    {
      path: '/admin/showcase',
      name: 'Showcase',
      component: Showcase,
    },
    {
      path: '/admin/product',
      name: 'ProductAdmin',
      component: ProductAdmin,
    },
    {
      path: '/admin/product/new',
      name: 'ProductAdminUnit',
      component: ProductAdminUnit,
    },
    {
      path: '/admin/product/:id',
      name: 'ProductAdminUpdateUnit',
      component: ProductAdminUnit,
      props: true
    },
    {
      path: '/admin/productattributes',
      name: 'ProductAttribute',
      component: ProductAttributes,
      props: true
    },
    {
      path: '/admin/productattributes/new',
      name: 'ProductAttribute',
      component: ProductAttributesUnit,
      props: true
    },
    {
      path: '/admin/productattributes/:id',
      name: 'ProductAttribute',
      component: ProductAttributesUnit,
      props: true
    },
    {
      path: '/admin/showcase/new',
      name: 'ShowcaseUnit',
      component: ShowcaseUnit,
    },
    {
      path: '/admin/showcase/:id',
      name: 'ShowcaseUpdateUnit',
      component: ShowcaseUnit,
      props: true
    },
     {
      path: '/admin/reward',
      name: 'Reward',
      component: Reward,
    },
    {
      path: '/admin/reward/new',
      name: 'RewardUnit',
      component: RewardUnit,
    },
    {
      path: '/admin/reward/:id',
      name: 'RewardUpdateUnit',
      component: RewardUnit,
      props: true
    },
    {
      path: '/admin/rewardreport',
      name: 'Report',
      component: Report,
      props: true
    },
    {
      path: '/admin/archivesmanagement',
      name: 'archives',
      component: ArchivesManagement
    },
    {
      path: '/admin/archivesmanagement/:currentTab',
      name: 'archives1',
      component: ArchivesManagement,
      props:true
    },
    {
      path: '/admin/author/new',
      name: 'AutherUnit',
      component: AuthorUnit
    },
    {
      path: '/admin/author/:id',
      name: 'AutherUnit',
      component: AuthorUnit,
      props: true
    },
    {
      path: '/admin/categorycomponent/new',
      name: 'CategoryComponentUnit',
      component: CategoryComponentUnit
    },
    {
      path: '/admin/categorycomponent/:id',
      name: 'CategoryComponentUnit',
      component: CategoryComponentUnit,
      props: true
    },
    {
      path: '/admin/articlecomponent/new',
      name: 'ArticleComponentUnitnew',
      component: ArticleComponentUnit
    },
    {
      path: '/admin/articlecomponent/:id',
      name: 'ArticleComponentUnit',
      component: ArticleComponentUnit,
      props: true
    }
  ],
});



