<template>
  <div class="row">
    <div class="catalog-topzone col-sm-9">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <a href="#/admin">Home</a>
        </li>
        <li class="breadcrumb-item active">Retailer Landing Page Carousel</li>
      </ol>
      <form class="form-horizontal" enctype="multipart/form-data" id=''>
        <div class="form-group">
            <label class="control-label col-sm-2" for="wishlist">Wishlist</label>
            <div class="col-sm-10">
                <input type="text" class="form-control" v-model="carouselItem.name">
            </div>
        </div>
        <div class="form-group">
            <label class="control-label col-sm-2" for="startdate">Start Date</label>
            <div class="col-sm-10">
                <input type="date" class="form-control" v-model="carouselItem.startDate">
            </div>
        </div>
          <div class="form-group">
            <label class="control-label col-sm-2" for="enddate">End Date</label>
            <div class="col-sm-10">
                <input type="date" class="form-control" v-model="carouselItem.endDate">
            </div>
        </div>
          <div class="form-group">
            <label class="control-label col-sm-2" for="speed">Scroller Speed</label>
            <div class="col-sm-10">
                <!-- <input type="number" class="form-control" v-model="carouselItem.speed" min='1' max='10'> -->
                <select class="form-control" v-model="carouselItem.speed">
                  <option value="1" > 1 </option>
                  <option value="2" > 2 </option>
                  <option value="3" > 3 </option>
                  <option value="4" > 4 </option>
                  <option value="5" > 5 </option>
                  <option value="6" > 6 </option>
                  <option value="7" > 7 </option>
                  <option value="8" > 8 </option>
                  <option value="9" > 9 </option>
                  <option value="10" > 10 </option>
                </select>
            </div>
        </div>
           <div class="form-group">
                <label class="control-label col-sm-2" for="email">Status</label>
                <div class="col-sm-10">
                    <select v-model="carouselItem.status" class="form-control">
                        <option value='Active'>Active</option>
                        <option value='Inactive'>Inactive</option>
                         <option value='draft'>Draft</option>
                    </select>
                </div>
            </div>
        <div class="form-group">
            <div class="col-sm-offset-2 col-sm-10">
                <button class="btn btn-success" type='submit' @click="saveWishlist">Save</button>
                <a class="btn btn-secondary" href='#/admin/retailercarousel'>Back</a>
            </div>
        </div>
      </form>
    </div>
    <div class="col-sm-3">
      <common-menu section='retailercarousel'></common-menu>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import CommonMenu from "../CommonMenu";
import { baseApi } from "../../../config";
export default {
  props: ["id"],
  data: function() {
    return {
      carouselItem: {
        carousel:true,
        wishlist_type:'saved'
      }
    };
  },
  methods: {
    formatDate(date) {
      return moment.utc(date).format("YYYY-MM-DD");
    },
    getModifiedData(carouselItem){
      let vm = this;
      [
        { key: "start_date", value: "startDate" },
        { key: "end_date", value: "endDate" }
      ].forEach(function(obj) {
        if (carouselItem[obj.key]) {
          var start_date = new Date(carouselItem[obj.key]);
          carouselItem[obj.value] = vm.formatDate(start_date);
        }
      });
      return carouselItem;
    },
    saveWishlist: function() {
      console.log("Calling Save menu");
      var vm = this;
      if(vm.carouselItem.startDate){
        vm.carouselItem.start_date = vm.carouselItem.startDate
      }
      if(vm.carouselItem.endDate){
        vm.carouselItem.end_date = vm.carouselItem.endDate
      }
      var sd = new Date(vm.carouselItem.start_date).getTime();
      var ed = new Date(vm.carouselItem.end_date).getTime();
      if(sd > ed) {
        console.log('Start date should not greater than end date.');
        vm.$notify({
          title: 'ERROR',
          text:'Start date should not greater than end date.',
          type: 'error'
        });
        return false;
      }
      if (vm.carouselItem._id) {
        axios
          .put(baseApi + "/wishLists/" + vm.carouselItem._id, vm.carouselItem)
          .then(function(response) {
            console.log(response);
            console.log("hit");
            vm.show = false;
          })
          .catch(function(error) {
            console.log("tes", error);
          });
      } else {
        axios
          .post(baseApi + "/wishLists", vm.carouselItem)
          .then(function(response) {
            console.log(response);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    fetchData: function() {
      var vm = this;
      axios
        .get(baseApi + "/wishLists/" + this.id)
        .then(function(response) {
          vm.carouselItem = vm.getModifiedData(response.data);
        })
        .catch(function(error) {
          console.log("error", error);
        });
    }
  },

  mounted: function() {
    console.log("mounted retailerCarouselItem");
  },
  created: function() {
    console.log("created retailerCarouselItem");
    this.fetchData();
  },
  updated: function() {


    console.log("updated retailerCarouselItem");
  },
  components: {
    CommonMenu
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}
</style>



