  <template>
    <div class="row">
      <div class="catalog-topzone col-sm-9">
        <ol class="breadcrumb">
          <li class="breadcrumb-item"> <a href="#/admin">Home</a> </li>
          <li class="breadcrumb-item active">Product Attribute</li>
        </ol>

        <a :href="'#/admin/productAttributes/new'" v-bind:class="{'btn-success':true}" class="btn" style="float:right; color:#fff; margin-bottom:15px;"> Add </a>
        <vue-good-table :columns="columns" :rows="rows" :paginate="true" :paginateOnTop="false" :lineNumbers="true">
            <template slot="table-row" slot-scope="props">
                <td>{{props.row.name}}</td>
                <td>{{props.row.slug}}</td>
                <td><a :href="'#/admin/productAttributes/' + props.row._id" class="btn btn-warning">Edit</a></td>
            </template>
        </vue-good-table>

        <!-- <table class="table">
          <thead>
            <tr>
              <th>Name</th>
              <th>Slug</th>
              <th> <a href="#/admin/productAttributes/new" v-bind:class="{'btn-success':true}" class="btn"> Add </a> </th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="productAttribute in productAttributes" :key="productAttribute._id">
              <td>{{productAttribute.name}}</td>
              <td>{{productAttribute.slug}}</td>
              <td>
                <button class="btn btn-danger hide" v-on:click="deleteProductAttribute(productAttribute._id)">
                  Delete
                </button>
                <a class="btn btn-warning" :href="'#/admin/productAttributes/'+productAttribute._id">
                  Edit
                </a>
              </td>
            </tr>
          </tbody>
        </table>

        <callout-modal v-if="show" @close="show=false"></callout-modal> -->
      </div>
      <div class="col-sm-3">
        <common-menu section='productAttributes'></common-menu>
      </div>

    </div>
  </template>

  <script>

    import axios from 'axios';
    import CommonMenu from '../CommonMenu';
    import { baseApi } from '../../../config';
    import Vue from 'vue';
    import VueGoodTable from 'vue-good-table';

    export default {
      data: function() {
        return {
          productAttributes: [],
          isDanger: true,
          show: false,
            columns: [{
                label: 'Name',
                field: 'name',
                placeholder: 'Filter By Name',
                filterable: true
            }, {
                label: 'Slug',
                field: 'slug',
                placeholder: 'Filter By Slug',
                filterable: true,
                html: false
            }, {
                label: 'Edit',
                html: true,
                sortable: false
            }],
            rows: []
        };
      },
      methods: {
        fetchData: function() {
          var vm = this;
          axios.get(baseApi + '/productAttributes')
            .then(function(response) {
              console.log('response', response);
              vm.productAttributes = response.data;
              vm.rows = response.data;
            })
            .catch(function(error) {
              console.log('error', error);
            })
        },
        deleteProductAttribute: function(id) {
          var vm = this;
          console.log('id', id);
          axios.delete(baseApi + '/productAttributes/' + id)
            .then(function(response) {
              console.log('response', response);
              vm.productAttributes = _.without(vm.productAttributes, _.find(vm.productAttributes, {
                id: id
              }));
              console.log('vm.productAttributes', vm.productAttributes.length)
            }).catch(function(error) {
              console.log('error', error);
            });
        }
      },
      mounted: function() {
      },
      created: function() {
        console.log('created');
        this.fetchData();
      },
      components: {
        CommonMenu
      }
    }
  </script>


