<template>
  <div class="row">

    <div class="catalog-topzone col-sm-9">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <a href="#/admin">Home</a>
        </li>
        <li class="breadcrumb-item active">Category</li>
      </ol>
      <paginate :page-count="page_count" :click-handler="clickHandlerCallback" class="pagination" :container-class="'className'">
        <span slot="prevContent"> Previous </span>
        <span slot="nextContent"> Next </span>
      </paginate>
      <table class="table">
        <thead>
          <tr>
            <th>Title</th>
            <th>SKU</th>
            <th> <a href="#/admin/product/new" v-bind:class="{'btn-success':true}" class="btn"> Add </a> </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="product in products" :key="product._id">
            <td>{{product.title}}</td>
            <td>{{product.sku}}</td>
            <td> <button class="btn btn-danger hide" v-on:click="deleteCallout(product._id)"> Delete </button>
              <a class="btn btn-warning" :href="'#/admin/product/'+product._id"> Edit </a>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="col-sm-3">
      <common-menu section='product'></common-menu>
    </div>
  </div>
</template>


<script>
  import axios from 'axios';
  import CommonMenu from '../CommonMenu';
  import { baseApi } from '../../../config';
  import Vue from 'vue';
  export default {
    data: function () {
      return {
        products: [],
        isDanger: true,
        page_count: 0,
        current_page: 1,
        show: false
      }
    },
    methods: {
      clickHandlerCallback: function (pageNum) {
        console.log("Hello -= =- =- -= " + pageNum);

        this.current_page = pageNum
        this.fetchData();
      },
      fetchData: function () {
        var vm = this;
        console.log("fetch method - -- - -  ");
        let url = baseApi + '/products?page=' + this.current_page;
        axios.get(url)
          .then(function(response) {
            console.log('response', response);
            var totCount = vm.current_page
          if(response.data.next){
           totCount = vm.current_page
            = Math.ceil(
            response.data.totalCount / (response.data.currentCount/(response.data.next-1))
          );
          }
            vm.page_count = totCount;
            vm.products = response.data.data;
          })
          .catch(function(error) {
            console.log('error', error)
          });
      },
      deleteUser: function(id) {
        var vm = this;
        console.log('id', id);
        axios.delete(baseApi + '/products/' + id)
          .then(function(response) {
            console.log('response', response);
            vm.callouts = _.without(vm.categories, _.find(vm.categories, {
              id: id
            }));
            console.log('vm.users', vm.categories.length)
          })
          .catch(function(error) {
            console.log('error', error);
          });
      },
      showModal: function(id) {
        if (id) router.push('/admin/category/' + id)
        else router.push('/admin/category/new')
      }
    },
    filters: {
      capitalize: function(value) {
        if (!value) return ''
        value = value.toString()
        return value.charAt(0).toUpperCase() + value.slice(1)
      }
    },
    mounted: function () {
        console.log('mounted');
    },
    created: function () {
        console.log('created');
        this.fetchData();
    },
    components: {
        CommonMenu
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
      font-weight: normal;
  }

  ul {
      list-style-type: none;
      padding: 0;
  }

  li {
      display: inline-block;
      margin: 0 10px;
  }

</style>




