<template>
    <div class="row">

        <div class="catalog-topzone col-sm-9">
            <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item active">Topzone</li>
        </ol>
            <table class="table ">
                <thead>
                    <tr>
                        <th>Url</th>
                        <th>Text</th>
                        <th>Image</th>
                        <th>Background Image</th>
                        <th>
                            <a href="#/admin/topzone/new " v-bind:class="{ 'btn-success':true} " class="btn ">
                                Add
                            </a>
                        </th>
                    </tr>
                </thead>
                <tbody>

                    <tr v-for="topzone in topzones " :key="topzone._id ">
                        <td>{{topzone.url}}</td>
                        <td>{{topzone.text}}</td>
                        <td>
                            <b-popover
                              v-if='topzone.image'
                              :target="'topzoneImage-' + topzone._id"
                              triggers="click blur">
                             <template slot="title">Preview</template>
                             <b-img :src="topzone.image" fluid alt="Responsive image" />
                            </b-popover>

                            <b-btn :id="'topzoneImage-' + topzone._id"
                              v-if='topzone.image'
                              variant="secondary"> Preview</b-btn>
                            <span v-if='!topzone.image'>N/A</span>
                        </td>
                        <td>

                          <b-popover
                            v-if='topzone.background_image'
                            :target="'topzoneBgImage-' + topzone._id"
                            triggers="click blur">
                           <template slot="title">Preview</template>
                           <b-img :src="topzone.background_image" fluid alt="Responsive image" />
                          </b-popover>

                          <b-btn :id="'topzoneBgImage-' + topzone._id"
                            v-if='topzone.background_image'
                            variant="secondary"> Preview</b-btn>
                          <span v-if='!topzone.background_image'>N/A</span>
                          
                        </td>
                        <td>
                            <button class="btn btn-danger " v-on:click="deleteCallout(topzone._id) ">
                                Delete
                            </button>
                            <a class="btn btn-warning " :href=" '#/admin/topzone/'+topzone._id ">
                                Edit
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="col-sm-3 ">
          <common-menu section='topzone'></common-menu>
        </div>
    </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
export default {
    data: function() {
        return {
            topzones: [],
            isDanger: true,
            show: false
        }
    },
    methods: {
        fetchData: function() {
            var vm = this;
            axios.get(baseApi + '/topzonePosts')
                .then(function(response) {
                    console.log('response', response);
                    vm.topzones = response.data;
                })
                .catch(function(error) {
                    console.log('error', error)

                })
        },
        deleteUser: function(id) {
            var vm = this;
            console.log('id', id);
            axios.delete(baseApi + '/topzonePosts/' + id)
                .then(function(response) {
                    console.log('response', response);
                    vm.callouts = _.without(vm.callouts, _.find(vm.callouts, {
                        id: id
                    }));
                    console.log('vm.users', vm.users.length)

                }).catch(function(error) {
                    console.log('error', error)

                })
        }
    },
    filters: {
        capitalize: function(value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    mounted: function() {
        console.log('mounted');

    },
    created: function() {
        console.log('created');
        this.fetchData();
    },
    components:{
        CommonMenu
    }
}
</script>


