<template>
    <div>
        <table class="table">
            <thead>
                <tr>
                    <th>Key</th>
                    <th>Value</th>
                    <th>
                        <button class="btn btn-success" v-on:click="editSetting()">
                            Add
                        </button>
                    </th>
                </tr>
            </thead>
            <tbody>

                <tr v-for="(value, key) in settings" :key="key">
                    <td>{{key}}</td>
                    <td>{{value}}</td>
                    <td>
                        <button class="btn btn-danger " v-on:click="deleteSetting(key)">
                            Delete
                        </button>
                        <button class="btn btn-warning" v-on:click="editSetting(key)">
                            Edit
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
        <key-value-modal @savesetting="savesetting" v-if='show' @close='close' :setting="setting" :class='{in:show}' v-bind:style="{ display: display}"></key-value-modal>
    </div>
</template>
<script>
import KeyValueModal from './KeyValueModal';
export default {
    data() {
        return {
            show: false,
            display: 'none',
            setting:{}
        }
    },
    props: ['settings'],
    computed: {

    },
    created() {

    },
    methods: {
        editSetting(key) {
            this.setting = {key:key,value:this.settings[key]};
            this.show = true;
             this.display = 'inline'
        },
        deleteSetting(key) {
            delete this.settings[key];
            this.$emit('save', this.settings);
        },
        savesetting(setting){
            this.settings[setting.key] = setting.value;
            this.$emit('save', this.settings);

        },
        close(){
            this.show = false;
             this.display = 'none'
        }
    },
    components:{
        KeyValueModal
    }
}
</script>


