<template>
    <div class="catalog-topzone">
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item">
                <a href="#/admin/brand">Showcase</a>
            </li>
            <li class="breadcrumb-item active" v-if='showcase._id'>{{showcase.name}}</li>
            <li class="breadcrumb-item active" v-else>New</li>
        </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="saveBrand" id='shwocaseunit'>

            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="showcase.name">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Slug</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="showcase.slug">
                </div>
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">optId</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="showcase.optId">
                </div>
            </div>
            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit'>Save</button>
                    <a class="btn btn-secondary" href='#/admin/showcase'>Back</a>
                </div>
            </div>
        </form>
    </div>
</template>

<script>

import axios from 'axios';
import _ from 'underscore';
import { baseApi } from '../../../config';
export default {
    data: function() {
        return {
           showcase:{}
        }
    },
    props: ['id'],
    created: function() {
        console.log('this', this.id)
        var vm = this;
        console.log('user instance created', this.$route.params)
        if (this.id) {
            axios.get(baseApi + '/showcases/' + this.id)
                .then(function(response) {
                    console.log('shwocase', response.data)
                    vm.showcase = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        }

    },
    // props:['id'],
    methods: {
        goBack: function() {
            router.push('/admin')
        },
        doThis: function() {
            console.log('do this')
            this.saveBrand()
            var vm = this;
        },
        saveBrand: function() {
            console.log('Calling Save User');
            var vm = this;
            if (vm.showcase._id) {
                axios.put(baseApi + '/showcases/' + vm.showcase._id, vm.showcase)
                    .then(function(response) {
                        console.log(response);

                        vm.show = false;
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else {
                axios.post(baseApi + '/showcases', vm.showcase)
                    .then(function(response) {
                        console.log(response);

                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            }

        },
    },
    components: {
    }
}
</script>


