  <template>
    <div class="reard-topzone">
      <ol class="breadcrumb">
        <li class="breadcrumb-item"> <a href="#/admin">Home</a> </li>
        <li class="breadcrumb-item"> <a href="#/admin/productAttributes">Product Attribute</a> </li>
        <li class="breadcrumb-item active" v-if='productAttribute._id'>{{productAttribute._id}}</li>
        <li class="breadcrumb-item active" v-else>New</li>
      </ol>

      <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="saveBrand" id='productAttributeunit'>

        <div class="form-group">
          <label class="control-label col-sm-2" for="pwd">Name</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" v-model="productAttribute.name">
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-2" for="pwd">Slug</label>
          <div class="col-sm-10" v-if='productAttribute._id'>
            <input type="text" class="form-control" v-model="productAttribute.slug">
          </div>
          <div class="col-sm-10" v-else>
            <input type="text" class="form-control" v-model="productAttribute.slug">
          </div>
        </div>
        <div class="form-group">
          <label class="control-label col-sm-2" for="pwd">Display Name</label>
          <div class="col-sm-10" v-if='productAttribute._id'>
            <input type="text" class="form-control" v-model="productAttribute.displayName">
          </div>
          <div class="col-sm-10" v-else>
            <input type="text" class="form-control" v-model="productAttribute.displayName">
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-2" for="email">Visibility</label>
          <div class="col-sm-10">
            <select v-model="productAttribute.visibility" class="form-control" multiple>
              <option disabled>Please Select visibility</option>
              <option value='LEFT'>LEFT</option>
              <option value='TOP'>TOP</option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <label class="control-label col-sm-2" for="email">Showcases</label>
          <div class="col-sm-10">
            <select v-model="productAttribute.showcases" class="form-control" multiple>
              <option disabled>Please Select showcae</option>
              <option v-for="obj in showcases" v-bind:value="obj.slug" :key='obj._id'>
                {{ obj.name }}
              </option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-10">
            <button class="btn btn-success" type='submit'>Save</button>
            <a class="btn btn-secondary" href='#/admin/productAttributes'>Back</a>
          </div>
        </div>
      </form>

    </div>
  </template>

  <script>

  import axios from 'axios';
  import _ from 'underscore';
  import { baseApi } from '../../../config';
  import CrudKeyValueModal from '../../common/keyvaluecrud';
  export default {
      data: function() {
        return {
          productAttribute: {
            ispointactivity:false,
            status:'active'
          },
          show: false,
          showcases: []
        }
      },
      props: ['id'],
      created: function() {
        console.log('this', this.id)
        var vm = this;
        console.log('user instance created', this.$route.params)
        if (this.id) {
          axios.get(baseApi + '/productAttributes/' + this.id)
            .then(function(response) {
              console.log('callout', response.data)
              var data = response.data;
              vm.productAttribute = data
            }).catch(function(error) {
              console.log('error', error)
            })
        }
        this.getShowcases();
      },
      methods: {
        goBack: function() {
          router.push('/admin')
        },
        updateSettings(settings) {
          console.log('Settings', settings)
          this.brand.settings = settings;
          this.saveBrand()
        },
          getShowcases() {
            let vm = this;
            axios.get(baseApi + '/showcases')
              .then(function(response) {
                vm.showcases = response.data;
              }).catch(function(error) {
                console.log('error', error)
              })
          },
          saveBrand: function() {
            console.log('Calling Save User');
              var vm = this;
              if (vm.productAttribute._id) {
                axios.put(baseApi + '/productAttributes/' + vm.productAttribute._id, vm.productAttribute)
                  .then(function(response) {
                    console.log(response);
                    vm.show = false;
                  })
                  .catch(function(error) {
                    console.log(error);
                  });
              } else {
                axios.post(baseApi + '/productAttributes', vm.productAttribute)
                  .then(function(response) {
                    console.log(response);
                  })
                  .catch(function(error) {
                    console.log(error);
                  });
              }
          },
      },
      components: {
          Keyvaluecrud: CrudKeyValueModal
      }
  }
  </script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    h1, h2 {
      font-weight: normal;
    }

    ul {
      list-style-type: none;
      padding: 0;
    }

    li {
      display: inline-block;
      margin: 0 10px;
    }
  </style>



