<template>
  <div class="row">

    <div class="catalog-topzone col-sm-9">
      <ol class="breadcrumb">
        <li class="breadcrumb-item">
          <a href="#/admin">Home</a>
        </li>
        <li class="breadcrumb-item active">Callout</li>
      </ol>
      <table class="table">
        <thead>
          <tr>
            <th>Retailer Id</th>
            <th>Retailer Name</th>
            <th>Showcase</th>

            <th>
              <a href="#/admin/callout/new" v-bind:class="{'btn-success':true}" class="btn">
                Add
              </a>
            </th>
          </tr>
        </thead>
        <tbody>

          <tr v-for="callout in callouts" :key="callout._id">
             <td>{{callout.retailer.id}}</td>
            <td>{{callout.retailer.name}}</td>
            <td>{{callout.showcase.name}}</td>
            <td>
              <button class="btn btn-danger" v-on:click="deleteCallout(callout._id)">
                Delete
              </button>
              <a class="btn btn-warning" :href="'#/admin/callout/'+callout._id">
                Edit
              </a>
            </td>
          </tr>
        </tbody>
      </table>
      <callout-modal v-if="show" @close="show=false"></callout-modal>
    </div>
    <div class="col-sm-3">
      <common-menu section='callout'></common-menu>
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import CalloutModal from './CalloutModal';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
export default {
  data: function() {
    return {
      callouts: [],
      isDanger: true,
      show: false,
      retailers:[]
    }
  },
  methods: {
    fetchData: function() {
      var vm = this;
      axios.get(baseApi + '/callouts')
        .then(function(response) {
          console.log('response', response);
          vm.callouts = response.data;
        })
        .catch(function(error) {
          console.log('error', error)

        })
    },
    getRetailers(optId) {
      axios.get(baseApi + '/retailers?optId='+optId)
        .then(function(response) {
          console.log('callout', response.data)
          vm.retailers = response.data;
        })
    },
    deleteCallout: function(id) {
      var vm = this;
      console.log('id', id);
      var flag = confirm("Do you want to delete this item?");
      if(flag) {
        axios.delete(baseApi + '/callouts/' + id)
          .then(function(response) {
            console.log('response', response);

            vm.fetchData();
          }).catch(function(error) {
            console.log('error', error);
          })
      }
    },
    showModal: function(id) {
      if (id) router.push('/admin/callout/' + id)
      else router.push('/admin/callout/new')
    }
  },
  filters: {
    capitalize: function(value) {
      if (!value) return ''
      value = value.toString()
      return value.charAt(0).toUpperCase() + value.slice(1)
    },
    getRetailerName(id) {
      return id;
    }
  },
  mounted: function() {
    console.log('mounted');

  },
  created: function() {
    console.log('created');
    this.fetchData();

  },
  updated: function() {
    console.log('updated')
  },
  components: {
    CalloutModal,
    CommonMenu
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

</style>



