<template>
    <div class="catalog-topzone">
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item">
                <a href="#/admin/topzone">Topzone</a>
            </li>
            <li class="breadcrumb-item active" v-if='topzone._id'>{{topzone._id}}</li>
            <li class="breadcrumb-item active" v-else>New</li>
        </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="doThis" id='topzoneform'>
            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Url</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" placeholder="eg./catalog/c/engagement-rings" v-model="topzone.url">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Text</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="topzone.text">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd"> Image</label>
                <div class="col-sm-9">
                    <input type="file" class="form-control" name='image'>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Background Image</label>
                <div class="col-sm-9">
                    <input type="file" class="form-control" name='background_image'>
                </div>
            </div>
            <div class="form-group hide">
                <label class="control-label col-sm-2" for="pwd">Image</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="topzone.image">
                </div>
            </div>
            <div class="form-group hide">
                <label class="control-label col-sm-2" for="pwd">Background Image</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="topzone.background_image">
                </div>
            </div>
            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit'>Save</button>
                    <a class="btn btn-secondary" href='#/admin/topzone'>Back</a>
                </div>
            </div>
        </form>

    </div>
</template>

<script>

import axios from 'axios';
import _ from 'underscore';
import { baseApi } from '../../../config';
export default {
    data: function() {
        return {
            topzone: {}
        }
    },
    props: ['id'],
    created: function() {
        console.log('this', this.id)
        var vm = this;
        console.log('user instance created', this.$route.params)
        if (this.id) {
            axios.get(baseApi + '/topzonePosts/' + this.id)
                .then(function(response) {
                    console.log('topzone', response.data)
                    vm.topzone = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        }
    },
    // props:['id'],
    methods: {
        goBack: function() {
            router.push('/admin')
        },
        saveTopzone: function() {
            console.log('Calling Save User');
            var vm = this;
            if (vm.topzone._id) {
                axios.put(baseApi + '/topzonePosts/' + vm.topzone._id, vm.topzone)
                    .then(function(response) {
                        console.log(response);

                        App.$router.push('/admin/topzone')
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else {
                axios.post(baseApi + '/topzonePosts', vm.topzone)
                    .then(function(response) {
                        console.log(response);
                        App.$router.push('/admin/topzone')
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            }

        },
        doThis: function() {
            console.log('do this')
            //here do what u want
            var vm = this;
            if (document.querySelector('[name=image]').value || document.querySelector('[name=background_image]').value) {
                const formData = new FormData(document.querySelector('#topzoneform'));
                let url = baseApi + '/commons/upload';
                axios.post(url, formData)
                    // get data
                    .then(x => {
                        var data = x.data;
                        console.log('data', data)
                        var desktop = _.findWhere(data, { key: 'image' });
                        var mobile = _.findWhere(data, { key: 'background_image' });
                        if (desktop) vm.topzone.image = desktop.value;
                        if (mobile) vm.topzone.background_image = mobile.value;
                        vm.saveTopzone();

                    }).catch(e => {
                        console.log('Something went wring', e)
                    })
                return false;
            } else {
                this.saveTopzone();
            }
        }
    }
    }
</script>


