<template>
  <div class="catalog-topzone">
    <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item">
        <a href="#/admin/archivesmanagement">Archive</a>
      </li>
      <li class="breadcrumb-item active" v-if='author._id'>{{author.name}}</li>
      <li class="breadcrumb-item active" v-else>New</li>
    </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="checkForm" id='autherunit'>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="author.name" name='name'>
                    <span v-show="errors.has('name')" class="red">{{ errors.first('name') }}</span>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Email</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="'required|email'" v-model="author.email" name='email'>
                    <span v-show="errors.has('email')" class="red">{{ errors.first('email') }}</span>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Phone</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="author.phone" name='phone'>
                    <span v-show="errors.has('phone')" class="red">{{ errors.first('phone') }}</span>
                </div>
            </div>
            <div class="form-group hide">
                <label class="control-label col-sm-2" for="pwd">Profile </label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="author.photo">
                    
                </div>
            </div>
            <div class="form-group">
              <label class="control-label col-sm-2" for="pwd">Profile Picture</label>
              <div class="col-sm-6">
                 <input v-if='author.photo' type="file" class="form-control"  name='profile_picture'>
                  <input v-else type="file" class="form-control" v-validate="{ required: true}"  name='profile_picture'>
                <span v-show="errors.has('profile_picture')" class="red">Please upload profile picture</span>
              </div>
              <div class="col-sm-3">

                <b-popover
                  v-if='author.photo'
                  :target="'bannerImage-' + author._id"
                  triggers="click blur">
                 <template slot="title">Preview</template>
                 <b-img :src="author.photo" fluid alt="Responsive image" />
                </b-popover>

                <b-btn :id="'bannerImage-' + author._id"
                  v-if='author.photo'
                  variant="secondary"> Preview</b-btn>
                <span v-if='!author.photo'>N/A</span>

              </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Position</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="author.position" name='position'>
                    <span v-show="errors.has('position')" class="red">{{ errors.first('position') }}</span>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Company</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-validate="{ required: true}" v-model="author.company.name"  name='company'>
                    <span v-show="errors.has('company')" class="red">{{ errors.first('company') }}</span>
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Bio</label>
                <div class="col-sm-10">
                    <textarea  class="form-control" rows='4' v-model="author.Bio" name='bio' v-validate="{ required: true}"/>
                    <span v-show="errors.has('bio')" class="red">{{ errors.first('bio') }}</span>
                </div>
            </div>
              <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Website Links</label>

                <div class="col-sm-10">
                  <input type='text' v-model="author.links"  class="form-control" placeholder="separated by comma" v-validate="{ required: true}" name='links'/>
                  <span v-show="errors.has('links')" class="red">{{ errors.first('links') }}</span>
                </div>
            </div>
            <legend>Social Website Links</legend>
             <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Facebook</label>
                <div class="col-sm-10">
                    <input type='text'  class="form-control" rows='4' v-model="author.social.facebook"/>
                </div>
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Twitter</label>
                <div class="col-sm-10">
                    <input type='text' class="form-control" rows='4' v-model="author.social.twitter"/>
                </div>
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Tumblr</label>
                <div class="col-sm-10">
                    <input type='text'  class="form-control" rows='4' v-model="author.social.tumblr"/>
                </div>
            </div>
             <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Pinterest</label>
                <div class="col-sm-10">
                    <input type='text'  class="form-control" rows='4' v-model="author.social.pinterest"/>
                </div>
            </div>
          
            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit' >Save</button>
                    <a  class="btn btn-default"  v-on:click="$router.push({path:`/admin/archivesmanagement/author`})">Cancel</a>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import axios from "axios";
import _ from "underscore";
import { baseApi } from "../../../config";
import Selectize from "vue2-selectize";
export default {
  components: {
    Selectize
  },
  data: function() {
    return {
      author: {
        social: {},
        company: {}
      }
    };
  },
  props: ["id"],
  created: function() {
    console.log("this id calling");
    this.fetchData();
  },
  methods: {
    doThis() {
      var vm = this;
      if (
        document.querySelector("[name=profile_picture]").value
      ) {
        const formData = new FormData(document.querySelector("#autherunit"));
        let url = baseApi + "/commons/upload";
        axios
          .post(url, formData)
          // get data
          .then(x => {
            var data = x.data;
            console.log("data", data);
            var profile_picture = _.findWhere(data, { key: "profile_picture" });
            var banner_image = _.findWhere(data, { key: "banner_image" });

            if (profile_picture) vm.author.photo = profile_picture.value;
            vm.saveAuther();
          })
          .catch(e => {
            console.log("Something went wring", e);
          });
        return false;
      } else {
        this.saveAuther();
      }
    },
    fetchData: function() {
      console.log("id ddd", this.id);
      var vm = this;
      if(this.id){
         axios
        .get(baseApi + "/archiveAuthors/" + this.id)
        .then(function(response) {
          console.log("callout", response.data);

          vm.author = response.data;
          if (vm.author && !vm.author.company) {
            vm.author.company = {};
          }
          if (vm.author && !vm.author.social) {
            vm.author.social = {};
          }
        })
        .catch(function(error) {
          console.log("error", error);
        });
      }
     
    },
     checkForm() {
      var vm = this;
      this.$validator.validateAll().then(res => {
        if (res) {
          vm.doThis()
        } else {
          alert("Please correct all error!");
        }
      });
    },
    saveAuther: function() {
      var vm = this;
      console.log(vm.author);
      if (this.author._id) {
        axios
          .put(baseApi + "/archiveAuthors/" + vm.author._id, vm.author)
          .then(function(resp) {
            console.log("response  ", resp);
          })
          .catch(function(err) {
            console.log("Error  ", err);
          });
      } else {
        axios
          .post(baseApi + "/archiveAuthors", vm.author)
          .then(function(resp) {
            console.log("response  ", resp);
            vm.$router.push({path:`/admin/archivesmanagement/author`})
          })
          .catch(function(err) {
            console.log("Error  ", err);
          });
      }
    }
  }
};
</script>
<style>
.red{
  color:red
}
</style>


