<template>
    <div id="myModal" class="modal fade" role="dialog">
        <div class="modal-dialog">

            <!-- Modal content-->
            <div class="modal-content">
                <div class="modal-header">
                  
                    <h4 class="modal-title" v-if='headermenu._id'>Edit</h4>
                    <h4 class="modal-title" v-else>Add</h4>
                      <button type="button" class="close" data-dismiss="modal" @click="$emit('close')">&times;</button>
                </div>
                <div class="modal-body">
                    <form class="form-horizontal">
                        <div class="form-group">
                            <label class="control-label col-sm-3" for="email">Anchor Link</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="headermenu.anchorLink">
                            </div>
                        </div>
                        <div class="form-group">
                            <label class="control-label col-sm-3" for="email">Label</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="headermenu.label">
                            </div>
                        </div>
                         <div class="form-group">
                            <label class="control-label col-sm-3" for="email">Column</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="headermenu.templatecolumn">
                            </div>
                        </div>
                        <div class="form-group">
                            <div class="col-sm-offset-3 col-sm-9">
                                <button class="btn btn-success" v-on:click="saveHeaderMenu">Save</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
export default {
    props: ['headermenu'],
    data() {
        return {
            show: false
        };
    },
    created() {
       console.log('headermenu',this.headermenu)
    },
    methods: {
        close: function () {

        },
        saveHeaderMenu: function () {

            console.log('saveHeaderMenu')
            this.$emit('saveheadermenu', this.headermenu);

            // this.close();
        }
    },
    updated() {
        console.log('callout11', this.headermenu)
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .modal-content{
     position: absolute;
 }
</style>



