import axios from "axios";
import CommonMenu from "../CommonMenu";
import AuthorComponent from "./AuthorComponent";
import ArticleComponent from "./ArticleComponent";
import CategoryComponent from "./CategoryComponent";
import { baseApi } from "../../../config";

export default {
  data: function() {
    return {
      // currentTab:'article'
    };
  },
  props: {
    currentTab: {
      default: "article",
      type: String
    }
  },
  methods: {},
  mounted: function() {
    console.log("mounted");
  },
  created: function() {
    console.log("created");
    // if (!this.currentTab) this.currentTab = "article";
  },
  components: {
    AuthorComponent,
    CommonMenu,
    ArticleComponent,
    CategoryComponent
  }
};
</script>


