<template>
    <div id="menumodal" class="modal fade" role="dialog">
        <div class="modal-dialog">

            <!-- Modal content-->
            <div class="modal-content" style="position:absolute">
                <div class="modal-header">

                    <h4>{{$parent.menu.label}}/ {{menuunit.label}} Listing</h4>
                    <button type="button" class="close pull-right" data-dismiss="modal" @click="$emit('close')">&times;</button>
                </div>
                <div class="modal-body">

                    <table class="table">
                        <thead>
                            <tr>
                                <th>anchorLink</th>
                                <th>Text</th>
                                <th></th>
                                <th>
                                    <button v-bind:class="{'btn-success':true}" class="btn" @click="addnewrow">
                                        Add
                                    </button>
                                </th>
                            </tr>
                        </thead>
                        <draggable 
                        v-model="menuunit.listing" 
                        :options="{group:'people'}" 
                        element="tbody"
                        @start="drag=true" 
                        @end="savelistings">
                            <tr v-for="listobj in menuunit.listing" :key="listobj._id">
                                <td><input type='text' class="form-control" v-model="listobj.anchorLink" /></td>
                                <td><input type='text' class="form-control" v-model="listobj.anchorText" /></td>
                                <td>
                                    <button class="btn btn-link" @click="showListings(listobj._id)">Show Listings</button>
                                </td>
                                <td>
                                    <button class="btn btn-danger" v-on:click="deleteListing(listobj._id)">
                                        Delete
                                    </button>
                                    <button class="btn btn-secondary" @click="updateListing(listobj._id)">
                                        save
                                    </button>
                                </td>
                            </tr>
                        </draggable>
                    </table>

                </div>

            </div>
        </div>
        <listings-modal @close="showlistings=false" v-if='showlistings' :listing="listing" @savelistinglistings='savelistinglistings' :class='{in:showlistings}' v-bind:style="{ display: display}"></listings-modal>
    </div>
</template>

<script>
import ListingsModal from './ListingsModal'
import draggable from 'vuedraggable'
import axios from 'axios';
import { baseApi } from '../../../config';
import _ from 'underscore';
export default {
    props: ['menuunit'],
    data() {
        return {
            show: false,
            display: 'inline',
            showlistings: false,
            listing: {}
        };
    },
    created() {
        console.log('menu', this.menu)
    },
    methods: {

        updateListing(listid) {

            this.$emit('savelistings', this.menuunit);
        },
        showListings(listigid) {
            console.log('Callingddddd')
            var listing = {};
            if (listigid) {
                listing = _.findWhere(this.menuunit.listing, { _id: listigid })
            }
            this.listing = listing
            console.log('menu', listing);
            this.showlistings = true;
            this.display = 'inline';
            return false;
        },
        savelistinglistings(listing) {
            this.menuunit.listing = _.map(this.menuunit.listing, function(obj) {
                if (obj._id == listing._id) {
                    obj.listings = listing.listings;
                }
                return obj;
            })
            this.$emit('savelistings', this.menuunit);
        },
        addnewrow() {
            this.menuunit.listing.unshift({})
        },
        deleteListing(listid) {
            var isDelete = window.confirm('are you sure, you want to delete?');
            if (isDelete) {
                this.menuunit.listing = _.filter(this.menuunit.listing, function(list) {
                    if (list._id != listid) return true;
                    return false;
                })
                this.$emit('savelistings', this.menuunit);
            }

        },
        savelistings() {
            this.$emit('savelistings', this.menuunit);
        }
    },
    updated() {
        console.log('callout11', this.headermenu)
    },
    components: {
        draggable,
        ListingsModal

    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

</style>



