<template>
    <div class="row">

        <div class="catalog-topzone col-sm-9">
            <ol class="breadcrumb">
                <li class="breadcrumb-item">
                    <a href="#/admin">Home</a>
                </li>
                <li class="breadcrumb-item active">Category</li>
            </ol>
            <a :href="'#/admin/category/new'" v-bind:class="{'btn-success':true}" class="btn" style="float:right; color:#fff; margin-bottom:15px;"> Add </a>
            <vue-good-table :columns="columns" :rows="rows" :paginate="true" :paginateOnTop="false" :lineNumbers="true">
                <template slot="table-row" slot-scope="props">
                    <td>{{props.row.name}}</td>
                    <td>{{props.row.slug}}</td>
                    <td><a :href="'#/admin/category/' + props.row._id" class="btn btn-warning">Edit</a></td>
                </template>
            </vue-good-table>
            <!-- <table class="table">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Slug</th>

                        <th>
                            <a href="#/admin/category/new" v-bind:class="{'btn-success':true}" class="btn">
                                Add
                            </a>
                        </th>
                    </tr>
                </thead>
                <tbody>

                    <tr v-for="category in categories" :key="category._id">
                        <td>{{category.name}}</td>
                        <td>{{category.slug}}</td>
                        <td>
                            <button class="btn btn-danger hide" v-on:click="deleteCallout(category._id)">
                                Delete
                            </button>
                            <a class="btn btn-warning" :href="'#/admin/category/'+category._id">
                                Edit
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
            <callout-modal v-if="show" @close="show=false"></callout-modal> -->
        </div>
        <div class="col-sm-3">
            <common-menu section='categories'></common-menu>
        </div>
    </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
import Vue from 'vue';
import VueGoodTable from 'vue-good-table';

export default {
    data: function() {
        return {
            categories: [],
            isDanger: true,
            show: false,
            columns: [{
                label: 'Name',
                field: 'name',
                placeholder: 'Filter By Name',
                filterable: true
            }, {
                label: 'Slug',
                field: 'slug',
                placeholder: 'Filter By Slug',
                filterable: true,
                html: false
            }, {
                label: 'Edit',
                html: true,
                sortable: false
            }],
            rows: []
        }
    },
    methods: {
        fetchData: function() {
            var vm = this;
            axios.get(baseApi + '/categories')
                .then(function(response) {
                    console.log('response', response);
                    vm.categories = response.data;
                    vm.rows = response.data;
                })
                .catch(function(error) {
                    console.log('error', error)

                })
        },
        deleteUser: function(id) {
            var vm = this;
            console.log('id', id);
            axios.delete(baseApi + '/categories/' + id)
                .then(function(response) {
                    console.log('response', response);
                    vm.callouts = _.without(vm.categories, _.find(vm.categories, {
                        id: id
                    }));
                    console.log('vm.users', vm.categories.length)

                }).catch(function(error) {
                    console.log('error', error)

                })
        },
        showModal: function(id) {
            if (id) router.push('/admin/category/' + id)
            else router.push('/admin/category/new')
        }
    },
    filters: {
        capitalize: function(value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    mounted: function() {
        console.log('mounted');

    },
    created: function() {
        console.log('created');
        this.fetchData();
    },
    components: {
        CommonMenu
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

</style>



