<template>
    <div class="row">

        <div class="catalog-topzone col-sm-9">
            <ol class="breadcrumb">
                <li class="breadcrumb-item">
                    <a href="#/admin">Home</a>
                </li>
                <li class="breadcrumb-item active">Callout</li>
            </ol>
            <table class="table">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Activity</th>
                        <th>Points</th>
                        <th>Type</th>
                        <th>Status</th>
                        <th>
                            <a href="#/admin/reward/new" v-bind:class="{'btn-success':true}" class="btn">
                                Add
                            </a>
                        </th>
                    </tr>
                </thead>
                <tbody>

                    <tr v-for="reward in rewards" :key="reward._id">
                        <td>{{reward.name}}</td>
                        <td>{{reward.activityName}}</td>
                        <td><span v-if='reward.ispointactivity'>{{reward.points}}</span><span v-else>N/A</span></td>
                        <td>{{reward.activitype}}</td>
                        <td>{{reward.status}}</td>

                        <td>
                            <button class="btn btn-danger hide" v-on:click="deleteReward(reward._id)">
                                Delete
                            </button>
                            <a class="btn btn-warning" :href="'#/admin/reward/'+reward._id">
                                Edit
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
            <callout-modal v-if="show" @close="show=false"></callout-modal>
        </div>
        <div class="col-sm-3">
            <common-menu section='rewards'></common-menu>
        </div>

    </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
export default {
    data: function() {
        return {
            rewards: [],
            isDanger: true,
            show: false
        }
    },
    methods: {
        fetchData: function() {
            var vm = this;
            axios.get(baseApi + '/rewards')
                .then(function(response) {
                    console.log('response', response);
                    vm.rewards = response.data;
                })
                .catch(function(error) {
                    console.log('error', error)

                })
        },
        deleteUser: function(id) {
            var vm = this;
            console.log('id', id);
            axios.delete(baseApi + '/rewards/' + id)
                .then(function(response) {
                    console.log('response', response);
                    vm.rewards = _.without(vm.rewards, _.find(vm.rewards, {
                        id: id
                    }));
                    console.log('vm.rewards', vm.rewards.length)

                }).catch(function(error) {
                    console.log('error', error)

                })
        }
    },
    mounted: function() {
    },
    created: function() {
        console.log('created');
        this.fetchData();
    },
    components: {
        CommonMenu
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}
</style>



