<template>
  <div class="row">

    <div class="catalog-topzone col-sm-9">
      <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item active">Menus</li>
    </ol>
      <table class="table">
        <thead>
          <tr>
            <th>Retailer Id</th>
            <th>Retailer Name</th>
            <th>Showcase</th>

            <th>
              <a href="#/admin/menulist/new" v-bind:class="{'btn-success':true}" class="btn">
                Add
              </a>
            </th>
          </tr>
        </thead>
        <tbody>

          <tr v-for="menulist in menulists" :key="menulist._id">
            <td>{{menulist.retailer.id}}</td>
            <td>{{menulist.retailer.name}}</td>
            <td>{{menulist.showcase.name}}</td>
            <td>
              <button class="btn btn-danger" v-on:click="deleteMenuList(menulist._id)">
                Delete
              </button>
              <a class="btn btn-warning" :href="'#/admin/menulist/'+menulist._id">
                Edit
              </a>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="col-sm-3">
     <common-menu section='menu'></common-menu>
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
export default {
  data: function() {
    return {
      menulists: [],
      isDanger: true,
      show: false
    }
  },
  methods: {
    fetchData: function() {
      var vm = this;
      axios.get(baseApi + '/menuLists')
        .then(function(response) {
          vm.menulists = response.data;
        })
        .catch(function(error) {
          console.log('error', error)

        })
    },
    deleteMenuList: function(id) {
      var vm = this;
      console.log('id', id);
      axios.delete(baseApi + '/menuLists/' + id)
        .then(function(response) {
          console.log('response', response);
          vm.menulists = _.without(vm.menulists, _.find(vm.menulists, {
            id: id
          }));
          // console.log('vm.users', vm.menulists.length)

        }).catch(function(error) {
          console.log('error', error)

        })
    },
    showModal: function(id) {
      if (id) router.push('/admin/menulist/' + id)
      else router.push('/admin/menulist/new')
    }
  },
  filters: {
    capitalize: function(value) {
      if (!value) return ''
      value = value.toString()
      return value.charAt(0).toUpperCase() + value.slice(1)
    }
  },
  mounted: function() {
    console.log('mounted');

  },
  created: function() {
    console.log('created');
    this.fetchData();
  },
  updated: function() {
    console.log('updated')
  },
  components:{
    CommonMenu
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

</style>



