<template>
  <div class="catalog-topzone">
    <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item">
        <a href="#/admin/product">Product</a>
      </li>
      <li class="breadcrumb-item active" v-if='product._id'>{{product.title}}</li>
      <li class="breadcrumb-item active" v-else>New</li>
    </ol>

    <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="saveBrand" id='productunit'>

      <div class="form-group">
        <label class="control-label col-sm-2" for="pwd">Title</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="product.title">
        </div>
      </div>

      <div class="form-group">
        <label class="control-label col-sm-2" for="pwd">SKU</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="product.sku">
        </div>
      </div>
       <div class="form-group">
                <label class="control-label col-sm-2" for="email">Showcases</label>
                <div class="col-sm-10">
                    <select v-model="product.showcases" class="form-control" multiple>
                        <option disabled>Please Select showcae</option>
                        <option v-for="obj in showcases" v-bind:value="obj.slug" :key='obj._id'>
                            {{ obj.name }}
                        </option>
                    </select>
                </div>
            </div>

      <div class="form-group">
        <label class="control-label col-sm-2" for="pwd">Product URL</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="product.producturl">
        </div>
      </div>

      <div class="form-group">
        <label class="control-label col-sm-2" for="pwd">Company OptID</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="product.company.optName">
        </div>
      </div>
        <div class="form-group">
                <label class="control-label col-sm-2" for="email">Status</label>
                <div class="col-sm-10">
                    <select v-model="product.status" class="form-control" >
                        <option disabled>Please Select Status</option>
                        <option value='Active'>Active</option>
                        <option value='Inactive'>Inactive</option>
                    </select>
                </div>
            </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
          <button class="btn btn-success" type='submit'>Save</button>
          <a class="btn btn-secondary" href='#/admin/product'>Back</a>
        </div>
      </div>

    </form>
  </div>
</template>

<script>
  import axios from 'axios';
  import _ from 'underscore';
  import { baseApi } from '../../../config';
  export default {
    data: function () {
      return {
        product: {},
        showcases:[]
      }
    },
    props: ['id'],
    created: function () {
      console.log('this', this.id)
      var vm = this;
      console.log('user instance created', this.$route.params)
      if (this.id) {
        axios.get(baseApi + '/products/' + this.id)
          .then(function(response) {
            console.log('shwocase', response.data)
            vm.product = response.data;
          })
          .catch(function(error) {
            console.log('error', error);
          });
      }
      this.getShowcases();
    },
    methods: {
      goBack: function() {
        router.push('/admin')
      },
      doThis: function() {
        console.log('do this')
        this.saveBrand()
        var vm = this;
      },
      getShowcases() {
            let vm = this;
            axios.get(baseApi + '/showcases')
                .then(function(response) {
                    vm.showcases = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        },
      saveBrand: function() {
        console.log('Calling Save User');
        var vm = this;
        if (vm.product._id) {
          axios.put(baseApi + '/products/' + vm.product._id, vm.product)
            .then(function(response) {
              console.log(response);
              vm.show = false;
            })
            .catch(function(error) {
              console.log(error);
            });
        } else {
          axios.post(baseApi + '/products', vm.product)
            .then(function(response) {
              console.log(response);
            })
            .catch(function(error) {
              console.log(error);
            });
        }
      }
    },
    components: {

    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

</style>



