<template>
  <div class="row">
    <div class="catalog-topzone col-sm-9">
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item active">Brands</li>
        </ol>
        <a :href="'#/admin/brand/' + props" v-bind:class="{'btn-success':true}" class="btn" style="float:right; color:#fff; margin-bottom:15px;"> Add </a>
        <vue-good-table :columns="columns" :rows="rows" :paginate="true" :paginateOnTop="false">
            <template slot="table-row" slot-scope="props">
                <td>{{props.row.name}}</td>
                <td>{{props.row.slug}}</td>
                <td>

                  <b-popover
                    v-if='props.row.bio'
                    :target="'topZoneImage-' + props.row.slug"
                    triggers="click blur">
                   <template slot="title">Preview</template>
                   <b-img :src="props.row.top_zone_image" fluid alt="Responsive image" />
                  </b-popover>

                  <b-btn :id="'topZoneImage-' + props.row.slug"
                  v-if='props.row.top_zone_image'
                  variant="secondary">Preview</b-btn>
                  <span v-if='!props.row.top_zone_image'>N/A</span>

                </td>
                <td>

                  <b-popover
                    v-if='props.row.bio'
                    :target="'bioImage-' + props.row.slug"
                    triggers="click blur">
                   <template slot="title">Preview</template>
                   <b-img :src="props.row.bio.image" fluid alt="Responsive image" />
                  </b-popover>

                  <b-btn :id="'bioImage-' + props.row.slug"
                    v-if='props.row.bio'
                    variant="secondary"> Preview</b-btn>
                  <span v-if='!props.row.bio'>N/A</span>

                </td>
                <td>
                  <a :href="'#/admin/brand/' + props.row._id" class="btn btn-warning">Edit</a>
                </td>

            </template>
        </vue-good-table>
    </div>
    <div class="col-sm-3">
         <common-menu section='brands'></common-menu>
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
import Vue from 'vue';
import VueGoodTable from 'vue-good-table';

Vue.use(VueGoodTable);
export default {
    data: function() {
        return {
            brands: [],
            isDanger: true,
            show: false,
            columns: [{
                label: 'Name',
                field: 'name',
                placeholder: 'Filter By Name',
                filterable: true
            }, {
                label: 'Slug',
                field: 'slug',
                placeholder: 'Filter By Slug',
                filterable: true,
                html: false
            }, {
                label: 'Catalog Topzone Image',
                html: true,
                sortable: false
            }, {
                label: 'Brand Bio Image',
                html: true,
                sortable: false
            }, {
                label: 'Edit',
                html: true,
                sortable: false
            }],
            rows: []
        }
    },
    methods: {
        fetchData: function() {
            var vm = this;
            axios.get(baseApi + '/brands')
                .then(function(response) {
                    console.log('response', response);
                    vm.brands = response.data;
                    vm.rows = response.data;
                })
                .catch(function(error) {
                    console.log('error', error)

                })
        },
        deleteUser: function(id) {
            var vm = this;
            console.log('id', id);
            axios.delete(baseApi + '/brands/' + id)
                .then(function(response) {
                    console.log('response', response);
                    vm.callouts = _.without(vm.callouts, _.find(vm.callouts, {
                        id: id
                    }));
                    console.log('vm.users', vm.users.length)

                }).catch(function(error) {
                    console.log('error', error)

                })
        },
        showModal: function(id) {
            if (id) router.push('/admin/brand/' + id)
            else router.push('/admin/brand/new')
        }
    },
    filters: {
        capitalize: function(value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    mounted: function() {
        console.log('mounted');

    },
    created: function() {
        console.log('created');
        this.fetchData();
    },
    components: {
        CommonMenu
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



