<template>
    <div class="row">

        <div class="catalog-topzone col-sm-9">
            <ol class="breadcrumb">
                <li class="breadcrumb-item">
                    <a href="#/admin">Home</a>
                </li>
                <li class="breadcrumb-item active">Category</li>
            </ol>
            <table class="table">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Slug</th>
                        <th>Company Id</th>

                        <!-- <th>
                            <a href="#/admin/showcase/new" v-bind:class="{'btn-success':true}" class="btn hide">
                                Add
                            </a>
                        </th> -->
                    </tr>
                </thead>
                <tbody>

                    <tr v-for="showcase in showcases" :key="showcase._id">
                        <td>{{showcase.name}}</td>
                        <td>{{showcase.slug}}</td>
                        <td>{{showcase.optId}}</td>
                        <!-- <td>
                            <button class="btn btn-danger hide" v-on:click="deleteCallout(showcase._id)">
                                Delete
                            </button>
                            <a class="btn btn-warning" :href="'#/admin/showcase/'+showcase._id">
                                Edit
                            </a>
                        </td> -->
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="col-sm-3">
            <common-menu section='showcase'></common-menu>
        </div>
    </div>
</template>

<script>

import axios from 'axios';
import CommonMenu from '../CommonMenu';
import { baseApi } from '../../../config';
export default {
    data: function() {
        return {
            showcases: [],
            isDanger: true,
            show: false
        }
    },
    methods: {
        fetchData: function() {
            var vm = this;
            axios.get(baseApi + '/showcases')
                .then(function(response) {
                    console.log('response', response);
                    vm.showcases = response.data;
                })
                .catch(function(error) {
                    console.log('error', error)

                })
        },
        deleteUser: function(id) {
            var vm = this;
            console.log('id', id);
            axios.delete(baseApi + '/showcases/' + id)
                .then(function(response) {
                    console.log('response', response);
                    vm.callouts = _.without(vm.categories, _.find(vm.categories, {
                        id: id
                    }));
                    console.log('vm.users', vm.categories.length)

                }).catch(function(error) {
                    console.log('error', error)

                })
        },
        showModal: function(id) {
            if (id) router.push('/admin/category/' + id)
            else router.push('/admin/category/new')
        }
    },
    filters: {
        capitalize: function(value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    mounted: function() {
        console.log('mounted');

    },
    created: function() {
        console.log('created');
        this.fetchData();
    },
    components: {
        CommonMenu
    }
}
</script>



