<template>
  <div class="catalog-topzone">
    <ol class="breadcrumb">
      <li class="breadcrumb-item">
        <a href="#/admin">Home</a>
      </li>
      <li class="breadcrumb-item">
        <a href="#/admin/menulist">MenuList</a>
      </li>
      <li class="breadcrumb-item active" v-if='menulist._id'>{{menulist._id}}</li>
      <li class="breadcrumb-item active" v-else>New</li>
    </ol>
    <form class="form-horizontal">
      <div class="form-group">
        <label class="control-label col-sm-2" for="email">Showcase</label>
        <div class="col-sm-10">
          <select v-model="menulist.showcase" class="form-control" @change="getRetailers">
            <option disabled>Please Select showcae</option>
            <option v-for="obj in showcases" v-bind:value="obj._id" :key='obj._id'>
              {{ obj.name }}
            </option>
          </select>
        </div>
      </div>
      <div class="form-group">
        <label class="control-label col-sm-2" for="email">Retailer</label>
        <div class="col-sm-10">
          <select v-model="menulist.retailerId" class="form-control">
            <option v-for="obj in retailers" v-bind:value="obj.company_id" :key='obj.company_id'>
              {{ obj.name }}
            </option>
          </select>
        </div>
      </div>

      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
          <button class="btn btn-success" @click='saveMenu'>Save</button>
          <a class="btn btn-secondary" @click='goBack'>Back</a>
        </div>
      </div>
    </form>
    <div class="" v-if='menulist._id'>
      <legend>Header Menus</legend>
      <table class="table table-hover">
        <thead>
          <tr>
            <th>Label</th>
            <th>Column </th>
            <th>Anchor Link</th>
            <th></th>
            <th>
              <button v-bind:class="{'btn-success':true}" class="btn" @click="openHeaderMenuModal()">
                Add
              </button>
            </th>
          </tr>
        </thead>
        <draggable v-model="menulist.showcasemenus"
          :options="{group:'people'}"
          @start="drag=true"
          element="tbody"
          @end="saveMenu">
            <tr v-for="menu in menulist.showcasemenus" :key="menu._id">
              <td>{{menu.label}}</td>
              <td>{{menu.templatecolumn}}</td>
              <td>{{menu.anchorLink}}</td>
              <td>
                <button @click="showMenuModal(menu._id)" class="btn btn-link">Show Menus</button>
              </td>
              <td>
                <button class="btn btn-danger" v-on:click="deleteHeaderMenu(menu._id)">
                  Delete
                </button>
                <button class="btn btn-warning" @click="openHeaderMenuModal(menu._id)">
                  Edit
                </button>
              </td>
            </tr>
        </draggable>
      </table>
    </div>
    <header-menu-modal @saveheadermenu="saveheadermenu_unit" @close="show=false" v-if='show' :headermenu="headermenu" :class='{in:show}' v-bind:style="{ display: display}"></header-menu-modal>
    <menu-modal @close="showmenu=false" @savesortmenu='savesort' v-if='showmenu' :menu="menu" :class='{in:showmenu}' v-bind:style="{ display: display}"></menu-modal>
  </div>
</template>

<script>

import axios from 'axios';
import HeaderMenuModal from './HeaderMenuModal';
import MenuModal from './MenuModal';
import _ from 'underscore';
import { baseApi } from '../../../config';
import draggable from 'vuedraggable'
export default {
  data: function() {
    return {
      menulist: {},
      show: false,
      showcases: [],
      callouts: [],
      headermenu: null,
      display: 'none',
      showmenu: false,
      retailers: []
    }
  },
  props: ['id'],
  created: function() {
    console.log('this', this.id)
    var vm = this;
    console.log('user instance created 1dddd  ', this.$route.params)
    if (this.id) {
      axios.get(baseApi + '/menuLists/' + this.id)
        .then(function(response) {
          console.log('callout', response.data)
          vm.menulist = response.data;
        }).catch(function(error) {
          console.log('error', error)

        })
    }
    this.getShowcases();

  },
  // props:['id'],
  methods: {
   goBack: function() {
      history.back()
    },
    savesort(menu) {
      var vm = this;
      vm.menulist.showcasemenus = _.map(vm.menulist.showcasemenus, function(obj) {
        if (obj._id == menu._id) {
          return menu;
        }
        return obj;
      })
      this.saveMenu();
    },
    getRetailers() {
      var vm = this;
      let showcaseId = this.menulist.showcase;
      console.log('showcaseId', showcaseId)
      let showcase = _.findWhere(this.showcases, { _id: showcaseId });
      axios.get(baseApi + '/retailers?optId=' + showcase.optId)
        .then(function(response) {
          console.log('callout', response.data)
          vm.retailers = response.data;
        }).catch(function(error) {
          console.log('error', error)

        })
    },
    showMenuModal(id) {
      console.log('Callingddddd')
      var menu = {};
      if (id) {
        menu = _.findWhere(this.menulist.showcasemenus, { _id: id })
      }
      this.menu = menu
      console.log('menu', menu);
      this.showmenu = true;
      this.display = 'inline';
      return false;
    },
    saveheadermenu_unit(headermenu) {
      var vm = this;
      console.log('headermenu', headermenu);
      if (headermenu._id) {
        vm.menulist.showcasemenus = _.map(vm.menulist.showcasemenus, function(obj) {
          if (obj._id == headermenu._id) {
            return headermenu;
          }
          return obj;
        })
      } else {
        vm.menulist.showcasemenus.push(headermenu);
      }

      this.saveMenu()
    },
    getShowcases() {
      let vm = this;
      axios.get(baseApi + '/showcases')
        .then(function(response) {
          vm.showcases = response.data;
        }).catch(function(error) {
          console.log('error', error)

        })
    },
    saveMenu: function() {
      console.log('Calling Save menu');
      var vm = this;
      if (vm.menulist._id) {
        axios.put(baseApi + '/menuLists/' + vm.menulist._id, vm.menulist)
          .then(function(response) {
            console.log(response);

            vm.show = false;
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        axios.post(baseApi + '/menuLists', vm.menulist)
          .then(function(response) {
            console.log(response);
          })
          .catch(function(error) {
            console.log(error);
          });
      }

    },
    openHeaderMenuModal(id) {
      console.log('Callingddddd')
      var headermenu = {};
      if (id) {
        headermenu = _.findWhere(this.menulist.showcasemenus, { _id: id })
      }
      this.headermenu = headermenu
      console.log('callout', headermenu);
      this.show = true;
      this.display = 'inline'
    },
    deleteHeaderMenu(id) {
      var isdelete = window.confirm('are youe sure?')
      console.log('isdelete',isdelete)
      if(isdelete){
        let showcasemenu = _.findWhere(this.menulist.showcasemenus, { _id: id })
      this.menulist.showcasemenus = _.without(this.menulist.showcasemenus,showcasemenu);
      this.saveMenu();
      }

    }
  },
  components: {
    HeaderMenuModal,
    MenuModal,
    draggable,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

</style>



