<template>
    <div id="menumodal" class="modal fade" role="dialog">
        <div class="modal-dialog">

            <!-- Modal content-->
            <div class="modal-content" style="position:absolute">
                 <div class="modal-header">
                    <h4 >{{menu.label}} Menus </h4>

                      <button type="button" class="close pull-right" data-dismiss="modal" @click="$emit('close')">&times;</button>
                </div>
                <div class="modal-body">

                    <table class="table">
                        <thead>
                            <tr>
                                <th>anchorLink</th>
                                <th>Label</th>
                                <th></th>
                                <th>
                                    <button  v-bind:class="{'btn-success':true}" class="btn" @click="editMenuModal()">
                                        Add
                                    </button>
                                </th>
                            </tr>
                        </thead>
                        <draggable 
                         v-model="menu.menus" 
                         :options="{group:'people'}" 
                         element="tbody"
                         @start="drag=true" 
                         @end="saveSortMenu">
                         <tr v-for="menuunit in menu.menus" :key="menuunit._id">
                            <td>{{menuunit.anchorLink}}</td>
                            <td>{{menuunit.label}}</td>
                            <td><button @click="showListing(menuunit._id)" class="btn btn-link">Show Listing</button></td>
                            <td>
                                <button class="btn btn-danger" v-on:click="deleteMenuUnit(menuunit._id)">
                                    Delete
                                </button>
                                <button class="btn btn-warning" @click="editMenuModal(menuunit._id)">
                                    Edit
                                </button>
                            </td>
                          </tr>
                        </draggable>
                    </table>

                </div>

            </div>
        </div>
        <addedit-menu-modal @close="show=false" v-if='show' @savemenuedit='savemenuedit' :menuunit="menuunit" :class='{in:show}' v-bind:style="{ display: display}"></addedit-menu-modal>
        <listing-modal @close="showlisting=false" v-if='showlisting' :menuunit="menuunit" @savelistings='savemenuedit' :class='{in:showlisting}' v-bind:style="{ display: display}"></listing-modal>
    </div>
</template>

<script>

import axios from 'axios';
import { baseApi } from '../../../config';
import AddeditMenuModal from './AddeditMenuModal';
import ListingModal from './ListingModal';
import draggable from 'vuedraggable'
export default {
    props: ['menu'],
    data() {
        return {
            show: false,
            menuunit:null,
            display:'inline',
            showlisting:false
        };
    },
    created() {
       console.log('menu',this.menu)
    },
    methods: {
        saveSortMenu(){
            this.$emit('savesortmenu',this.menu);
        },
        savemenuedit(menuunit){
            console.log('menuunit',menuunit);
            if(menuunit._id){
                this.menu.menus = _.map(this.menu.menus,function(menuunitobj){
                if(menuunitobj._id == menuunit._id){
                    return menuunit;
                }
                return menuunitobj;
            })
            }else{
                this.menu.menus.push(menuunit)
            }

            this.$emit('savesortmenu',this.menu);
        },
        deleteMenuUnit(id){
            this.menu.menus = _.filter(this.menu.menus,function(obj){
                if(obj._id != id) return true;
                return false;
            })
            this.$emit('savesortmenu',this.menu);
        },
        showListing(menuid){
            let menuunit = {};
            if(menuid){
                menuunit = _.findWhere(this.menu.menus,{_id:menuid})
            }
            this.menuunit = menuunit
            this.showlisting = true;
            return false;
        },
        saveHeaderMenu: function () {

            console.log('saveHeaderMenu')
            this.$emit('saveheadermenu', this.headermenu);

            // this.close();
        },
        editMenuModal(id){
            let menuunit = {};
            if(id){
                menuunit = _.findWhere(this.menu.menus,{_id:id})
            }
            this.menuunit = menuunit
            this.show = true;

        }
    },
    updated() {
        console.log('callout11', this.headermenu)
    },
    components:{
        AddeditMenuModal,
        ListingModal,
        draggable
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

</style>



