<template>
    <div class="reard-topzone">
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
                <a href="#/admin">Home</a>
            </li>
            <li class="breadcrumb-item">
                <a href="#/admin/reward">Reward</a>
            </li>
            <li class="breadcrumb-item active" v-if='reward._id'>{{reward._id}}</li>
            <li class="breadcrumb-item active" v-else>New</li>
        </ol>
        <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="saveBrand" id='rewardunit'>

            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Name</label>
                <div class="col-sm-10">
                    <input type="text" class="form-control" v-model="reward.name">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Activity</label>
                <div class="col-sm-10" v-if='reward._id'>
                    <input type="text" class="form-control" v-model="reward.activityName" readonly>
                </div>
                <div class="col-sm-10" v-else>
                    <input type="text" class="form-control" v-model="reward.activityName">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Do you have to add points</label>
                <div class="col-sm-10">
                    <label class="radio-inline"><input type="radio" name="optradio" :value='true' v-model="reward.ispointactivity">Yes</label>
                    <label class="radio-inline"><input type="radio" name="optradio" :value='false' v-model="reward.ispointactivity">No</label>
                </div>
            </div>
            <div class="form-group" v-if='reward.ispointactivity'>
                <label class="control-label col-sm-2" for="pwd">Points</label>
                <div class="col-sm-10">
                    <input type="number" class="form-control" step='0.01' v-model="reward.points">
                </div>
            </div>
            <div class="form-group">
                <label class="control-label col-sm-2" for="pwd">Activity Type</label>
                <div class="col-sm-9" v-if='reward._id'>
                    <input type="text" class="form-control" v-model='reward.activitype' readonly>
                </div>
                <div class="col-sm-9" v-else>
                    <input type="text" class="form-control" v-model='reward.activitype'>
                </div>
            </div>

            <div class="form-group">
                <label class="control-label col-sm-2" for="email">Status</label>
                <div class="col-sm-10">
                    <select v-model="reward.status" class="form-control">
                        <option disabled>Please Select visibility</option>
                        <option value='active'>Active</option>
                        <option value='inactive'>Inactive</option>
                    </select>
                </div>
            </div>

            <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                    <button class="btn btn-success" type='submit'>Save</button>
                    <a class="btn btn-secondary" href='#/admin/reward'>Back</a>
                </div>
            </div>
        </form>

    </div>
</template>

<script>

import axios from 'axios';
import _ from 'underscore';
import { baseApi } from '../../../config';
import CrudKeyValueModal from '../../common/keyvaluecrud';
export default {
    data: function() {
        return {
            reward: {
                ispointactivity:false,
                status:'active'
            },
            show: false,
            showcases: [],
            callouts: [],
            childcallout: null,
            display: 'none'
        }
    },
    props: ['id'],
    created: function() {
        console.log('this', this.id)
        var vm = this;
        console.log('user instance created', this.$route.params)
        if (this.id) {
            axios.get(baseApi + '/rewards/' + this.id)
                .then(function(response) {
                    console.log('callout', response.data)
                    var data = response.data;
                    vm.reward = data
                }).catch(function(error) {
                    console.log('error', error)

                })
        }
        this.getShowcases();

    },
    // props:['id'],
    methods: {
        goBack: function() {
            router.push('/admin')
        },
        updateSettings(settings) {
            console.log('Settings', settings)
            this.brand.settings = settings;
            this.saveBrand()
        },
        getShowcases() {
            let vm = this;
            axios.get(baseApi + '/showcases')
                .then(function(response) {
                    vm.showcases = response.data;
                }).catch(function(error) {
                    console.log('error', error)

                })
        },
        doThis: function() {
            console.log('do this')
            //here do what u want
            var vm = this;
            if (document.querySelector('[name=topzone_image]').value || document.querySelector('[name=bio_image]').value) {
                const formData = new FormData(document.querySelector('#brandunit'));
                let url = baseApi + '/commons/upload';
                axios.post(url, formData)
                    // get data
                    .then(x => {
                        var data = x.data;
                        console.log('data', data)
                        var topzone = _.findWhere(data, { key: 'topzone_image' });
                        var bio = _.findWhere(data, { key: 'bio_image' });
                        if (topzone) vm.brand.top_zone_image = topzone.value;
                        if (bio) {
                            vm.brand.bio = vm.brand.bio || {};
                            vm.brand.bio.image = bio.value;
                        }
                        vm.saveBrand();

                    }).catch(e => {
                        console.log('Something went wring', e)
                    })
                return false;
            } else {
                this.saveBrand();
            }
        },
        saveBrand: function() {
            console.log('Calling Save User');
            var vm = this;
            if (vm.reward._id) {
                axios.put(baseApi + '/rewards/' + vm.reward._id, vm.reward)
                    .then(function(response) {
                        console.log(response);

                        vm.show = false;
                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            } else {
                axios.post(baseApi + '/rewards', vm.reward)
                    .then(function(response) {
                        console.log(response);

                    })
                    .catch(function(error) {
                        console.log(error);
                    });
            }

        },
    },
    components: {
        Keyvaluecrud: CrudKeyValueModal
    }
}
</script>



