<template>
  <div class="catalog-topzone col-sm-9">
    <table class="table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Display Name</th>
          <th>Slug</th>
          <th> <a href="#/admin/categorycomponent/new" v-bind:class="{'btn-success':true}" class="btn"> Add </a> </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="cate in categories" :key="cate._id">
          <td>{{cate.name}}</td>
          <td>{{cate.displayName}}</td>
          <td>{{cate.slug}}</td>
          <td>
            <a class="btn btn-warning" :href="'#/admin/categorycomponent/'+ cate._id"> Edit </a>
          </td>
          <td>
            <button class="btn btn-danger" v-on:click="deleteCategory(cate._id)"> Delete </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
import axios from 'axios';
import { baseApi } from '../../../config';

export default {
  data: function () {
    return {
      categories: []
    }
  },
  methods: {
    fetchData: function () {
      var vm = this;

      axios.get(baseApi + '/archiveCategories')
        .then(function(response) {
          console.log('-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-------=-=-=-=-=');
          console.log('response', response);
          vm.categories = response.data;
        })
        .catch(function(error) {
          console.log('error', error)
        });
    },
    deleteCategory: function (id) {
      var vm = this;

      console.log('category id ====>',id);

      var userConsent = confirm('Are you sure you want to delete this Category ?')
      
      if(!userConsent){
        return false;
      }

      axios.delete(baseApi + '/archiveCategories/' + id)
        .then(function(response) {
          console.log('-=-=-=-=-=-=-=-=-=-=-=- deleting category');
          console.log('response', response);
          vm.fetchData();
        })
        .catch(function(error) {
          console.log('error', error)
        });
    }
  },
  mounted: function() {
    console.log('mounted 654 654 654 6546546 ');
  },
  created: function() {
    console.log('created 66565656565  5 65 654745 754 ');
    this.fetchData();
  }
}
</script>

<style>

</style>



