// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import BootstrapVue from 'bootstrap-vue';
import App from './App';
import router from './router';
import axios from 'axios';
import Notifications from 'vue-notification'
import moment from 'moment-timezone'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
// import App from './App.vue'
import Paginate from 'vuejs-paginate'
import lang from 'element-ui/lib/locale/lang/en'
import locale from 'element-ui/lib/locale'
import VeeValidate from 'vee-validate';

Vue.use(VeeValidate);
// configure language
locale.use(lang)
Vue.component('paginate', Paginate)
Vue.use(ElementUI)
 Vue.use(BootstrapVue);
Vue.use(Notifications);

Vue.config.productionTip = false;

Vue.filter('formatDate', function(value) {
  if (value) {
    return moment.utc(String(value)).format("MMM Do YY")
  }
})
Vue.filter('getTimzoneDate',function(value){
  // Use moment js timezone library
  // Needs to change code
  return moment.utc(String(value)).format("MMM Do YY")
})
Vue.filter('humenDate', function(value) {
  if (value) {
    return moment.utc(String(value)).format('MMMM Do YYYY, h:mm:ss a')
  }
  return value;
})

/* eslint-disable no-new */
window.App = new Vue({
  el: '#optportalnode',
  router,
  template: '<App/>',
  components: { App },
});


