<template>
  <div class="catalog-topzone col-sm-9">
    <table class="table">
      <thead>
        <tr>
          <th>Name</th>
          <th>Email</th>
          <th>Phone</th>
          <th>Profile Picture</th>
          <th> <a href="#/admin/author/new" v-bind:class="{'btn-success':true}" class="btn btn-sm"> Add </a> </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="author in authors" :key="author._id">
          <td>{{author.name}}</td>
          <td>{{author.email}}</td>
          <td>{{author.phone}}</td>
          <td>
            <b-popover
                    v-if='author.photo'
                    :target="'topZoneImage-' + author._id"
                    triggers="click blur">
                   <template slot="title">Preview</template>
                   <b-img :src="author.photo" fluid alt="Responsive image" />
                  </b-popover>

                  <b-btn :id="'topZoneImage-' + author._id"
                  v-if='author.photo'
                  variant="secondary">Preview</b-btn>
                  <span v-if='!author.photo'>N/A</span>
            
          </td>
          <td>
            <a class="btn btn-warning btn-sm" :href="'#/admin/author/'+ author._id"> Edit </a>
          </td>
          <td>
            <button class="btn btn-danger btn-sm" v-on:click="deleteAuthor(author._id)"> Delete </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>  
</template>


<script>
import axios from 'axios';
import { baseApi } from '../../../config';

export default {
  data: function () {
    return {
      authors: []
    }
  },
  methods: {
    fetchData() {
      var vm = this;

      axios.get(baseApi + '/archiveAuthors')
        .then(function(response) {
          console.log('-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-------=-=-=-=-=');
          console.log('response', response);
          vm.authors = response.data;
        })
        .catch(function(error) {
          console.log('error', error)
        });
    },
    deleteAuthor(id) {
      var vm = this;
      console.log('id =====>',id);

      var userConsent = confirm('Are you sure you want to delete this Contributor ?')
      
      if(!userConsent){
        return false;
      }

      axios.delete(baseApi + '/archiveAuthors/' + id)
        .then(function(response) {
          console.log(' author deleted ');
          console.log('response', response);
          vm.fetchData();
        })
        .catch(function(error) {
          console.log('error', error)
        });
    }
  },
  mounted: function() {
    console.log('mounted 654 654 654 6546546 ');
  },
  created: function() {
    console.log('created 66565656565  5 65 654745 754 ');
    this.fetchData();
  }
}
</script>

<style>

</style>



