<template>
    <div id="myModal" class="modal fade" role="dialog">
        <div class="modal-dialog">

            <!-- Modal content-->
            <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" v-on:click="$parent.show=false">&times;</button>
                    <h4 class="modal-title" v-if='callout._id'>Edit</h4>
                    <h4 class="modal-title" v-else>Edit</h4>
                </div>
                <div class="modal-body">
                    <form class="form-horizontal" enctype="multipart/form-data" @submit.prevent="doThis" id='formunit'>
                        <div class="form-group">
                            <label class="control-label col-sm-3" for="email">Anchor Link</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="callout.anchorLink">
                            </div>
                        </div>
                        <div class="form-group hide">
                            <label class="control-label col-sm-3" for="email">Image Column</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="callout.imagecolumn">
                            </div>
                        </div>
                        <div class="form-group hide">
                            <label class="control-label col-sm-3" for="email">Mobile Image</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="callout.mobile_image">
                            </div>
                        </div>
                        <div class="form-group hide">
                            <label class="control-label col-sm-3" for="email">Desktop Image</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="callout.desktop_image">
                            </div>
                        </div>
                        <div class="form-group">
                            <label class="control-label col-sm-3" for="email">Image Column Offset</label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="callout.imagcolumnoffset">
                            </div>
                        </div>
                         <div class="form-group">
                            <label class="control-label col-sm-3" for="email">Image Column </label>
                            <div class="col-sm-9">
                                <input type="text" class="form-control" v-model="callout.imagecolumn">
                            </div>
                        </div>

                        <div class="form-group">
                            <label class="control-label col-sm-3" for="pwd">Desktop Image</label>
                            <div class="col-sm-9">
                                <input type="file" class="form-control" name='desktop_image'>
                            </div>
                        </div>
                        <div class="form-group">
                            <label class="control-label col-sm-3" for="pwd">Mobile Image</label>
                            <div class="col-sm-9">
                                <input type="file" class="form-control" name='mobile_image'>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="col-sm-offset-3 col-sm-9">
                                <button type='submit' class="btn btn-secondary">Save</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { baseApi } from '../../../config';
export default {
    props: ['callout'],
    data() {
        return {
            show: false
        };
    },
    created() {

    },
    methods: {
        saveCallout: function() {

            console.log('saveCallout')
            this.$emit('savecalloutunit', this.callout);

            // this.close();
        },
        doThis: function() {
            console.log('do this')
            //here do what u want
            var vm = this;
            if (document.querySelector('[name=desktop_image]').value || document.querySelector('[name=mobile_image]').value) {
                const formData = new FormData(document.querySelector('#formunit'));
                let url = baseApi + '/commons/upload';
                axios.post(url, formData)
                    // get data
                    .then(x => {
                        var data = x.data;
                        console.log('data',data)
                        var desktop = _.findWhere(data,{key:'desktop_image'});
                        var mobile =  _.findWhere(data,{key:'mobile_image'});
                        if(desktop)  vm.callout.desktop_image = desktop.value;
                        if(mobile)  vm.callout.mobile_image = mobile.value;
                        vm.saveCallout();

                    }).catch(e => {
                        console.log('Something went wring', e)
                    })
                    return false;
            }else{
                this.saveCallout();
            }

            // add url field
        }
    },
    updated() {
        console.log('callout11', this.callout)
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.modal-content {
    position: absolute;
}
</style>



